import {translationAnimationLoop} from "./translationAnimationLoop.js"
import {rotationAnimationLoop} from "./rotationAnimationLoop.js"
import {initializeTranslation} from "./initializeTranslation.js"
import {opacityAnimationLoop} from "./opacityAnimationLoop.js"
import {knowledgeCollectorUI} from "./knowledgeCollectorUI.js"
import {scaleAnimationLoop} from "./scaleAnimationLoop.js"
import {initializeRotation} from "./initializeRotation.js"
import {initializeOpacity} from "./initializeOpacity.js"
import {initializeScale} from "./initializeScale.js"
import { knowledgeCollector } from "../knowledgeCollector/knowledgeCollector.js"









const initializeAnimationLoop = () =>
{
    knowledgeCollector.animationProtectionShield = true

    if(knowledgeCollectorUI.previousLevel === "1" && knowledgeCollectorUI.currentLevel === "1")
    {
        initializeRotation("group_1", "groupInitialRotation_1")
        rotationAnimationLoop("group_1", "groupInitialRotation_1", "gropRotationCount_1")//lesezeichen call by value, wichtige zu vermittelnde information.
    }
    else if(knowledgeCollectorUI.previousLevel === "2" && knowledgeCollectorUI.currentLevel === "2")
    {
        initializeRotation("group_2", "groupInitialRotation_2")
        rotationAnimationLoop("group_2", "groupInitialRotation_2", "gropRotationCount_2")
    }
    else if(knowledgeCollectorUI.previousLevel === "3" && knowledgeCollectorUI.currentLevel === "3")//implementiert aber keine weiteren knoten in level3, wird nicht ueber nodeTravers aufgerufen
    {
        initializeRotation("group_3", "groupInitialRotation_3")
        rotationAnimationLoop("group_3", "groupInitialRotation_3", "gropRotationCount_3")
    }
    else if(knowledgeCollectorUI.previousLevel === "1" && knowledgeCollectorUI.currentLevel === "2")
    {
        initializeTranslation()
        translationAnimationLoop()

        initializeOpacity()
        opacityAnimationLoop("contentCarrier_1", "contentCarrier_2")
    }
    else if(knowledgeCollectorUI.previousLevel === "2" && knowledgeCollectorUI.currentLevel === "1")
    {
        initializeTranslation()
        translationAnimationLoop()

        initializeOpacity()
        opacityAnimationLoop("contentCarrier_2", "contentCarrier_1")
    }
    else if(knowledgeCollectorUI.previousLevel === "2" && knowledgeCollectorUI.currentLevel === "3")
    {
        initializeTranslation()
        translationAnimationLoop()

        initializeOpacity()
        opacityAnimationLoop("contentCarrier_2", "contentCarrier_3")
    }
    else if(knowledgeCollectorUI.previousLevel === "3" && knowledgeCollectorUI.currentLevel === "2")
    {
        initializeScale()
        scaleAnimationLoop()
    }
}









export
{
    initializeAnimationLoop
}