import {knowledgeCollector} from "./knowledgeCollector";
import {scene} from "../../script.js"









const generateGroupHierarchy = () =>
{
    knowledgeCollector.group_1.name = "group1"
    knowledgeCollector.group_2.name = "group2"
    knowledgeCollector.group_3.name = "group3"
    knowledgeCollector.group_4.name = "group4"
    knowledgeCollector.group_5.name = "group5"
    knowledgeCollector.group_6.name = "group6"

    knowledgeCollector.group_6.add(knowledgeCollector.group_5)
    knowledgeCollector.group_5.add(knowledgeCollector.group_4)
    knowledgeCollector.group_4.add(knowledgeCollector.group_3)
    knowledgeCollector.group_3.add(knowledgeCollector.group_2)
    knowledgeCollector.group_2.add(knowledgeCollector.group_1)

    knowledgeCollector.group_1.add(knowledgeCollector.projector_1)
    knowledgeCollector.group_1.add(knowledgeCollector.projector_2)
    knowledgeCollector.group_1.add(knowledgeCollector.wing_1_1)
    knowledgeCollector.group_1.add(knowledgeCollector.wing_1_2)
    knowledgeCollector.group_1.add(knowledgeCollector.wing_2_1)
    knowledgeCollector.group_1.add(knowledgeCollector.wing_2_2)
    knowledgeCollector.group_1.add(knowledgeCollector.wing_3_1)
    knowledgeCollector.group_1.add(knowledgeCollector.wing_3_2)
    knowledgeCollector.group_1.add(knowledgeCollector.wing_4_1)
    knowledgeCollector.group_1.add(knowledgeCollector.wing_4_2)
    knowledgeCollector.group_1.add(knowledgeCollector.wing_5_1)
    knowledgeCollector.group_1.add(knowledgeCollector.wing_5_2)
    knowledgeCollector.group_1.add(knowledgeCollector.wing_6_1)
    knowledgeCollector.group_1.add(knowledgeCollector.wing_6_2)

    knowledgeCollector.group_6.position.set(-3.42, -2, 43.35)

    scene.add(knowledgeCollector.group_6)
}









export
{
    generateGroupHierarchy
}