import {knowledgeCollector} from "../knowledgeCollector/knowledgeCollector"
import {knowledgeCollectorUI} from "./knowledgeCollectorUI"
import {AxesHelper} from "three"









const generateKnowledgeCollectorUIGroupHierarchy  = () =>
{
    knowledgeCollectorUI.group_0.name = "group_0"
    knowledgeCollectorUI.group_0_1.name = "group_0_1"
    knowledgeCollectorUI.group_1.name = "group_1"
    knowledgeCollectorUI.group_1_1.name = "group_1_1"
    knowledgeCollectorUI.group_1_2.name = "group_1_2"
    knowledgeCollectorUI.group_2.name = "group_2"
    knowledgeCollectorUI.group_2_1.name = "group_2_1"
    knowledgeCollectorUI.group_2_2.name = "group_2_2"
    knowledgeCollectorUI.group_3.name = "group_3"
    knowledgeCollectorUI.group_3_1.name = "group_3_1"
    knowledgeCollectorUI.group_3_2.name = "group_3"

    knowledgeCollectorUI.group_1.add(knowledgeCollectorUI.group_1_1, knowledgeCollectorUI.group_1_2)
    knowledgeCollectorUI.group_2.add(knowledgeCollectorUI.group_2_1, knowledgeCollectorUI.group_2_2)
    knowledgeCollectorUI.group_3.add(knowledgeCollectorUI.group_3_1, knowledgeCollectorUI.group_3_2)
    knowledgeCollectorUI.group_0.add(knowledgeCollectorUI.group_1, knowledgeCollectorUI.group_2, knowledgeCollectorUI.group_3)
    knowledgeCollectorUI.group_1.add(knowledgeCollectorUI.contentCarrier_1)
    knowledgeCollectorUI.group_2.add(knowledgeCollectorUI.contentCarrier_2)
    knowledgeCollectorUI.group_3.add(knowledgeCollectorUI.contentCarrier_3)
    // knowledgeCollectorUI.group_0_1.add(knowledgeCollectorUI.volume)//kosten ca 10%
    knowledgeCollectorUI.group_0_1.add(knowledgeCollectorUI.ringLeft_1)
    knowledgeCollectorUI.group_0_1.add(knowledgeCollectorUI.ringLeft_2)
    knowledgeCollectorUI.group_0_1.add(knowledgeCollectorUI.ringLeft_3)
    knowledgeCollectorUI.group_0_1.add(knowledgeCollectorUI.ringLeft_4)
    knowledgeCollectorUI.group_0_1.add(knowledgeCollectorUI.ringLeft_5)
    knowledgeCollectorUI.group_0_1.add(knowledgeCollectorUI.ringLeft_6)
    knowledgeCollectorUI.group_0_1.add(knowledgeCollectorUI.ringRight_1)
    knowledgeCollectorUI.group_0_1.add(knowledgeCollectorUI.ringRight_2)
    knowledgeCollectorUI.group_0_1.add(knowledgeCollectorUI.ringRight_3)
    knowledgeCollectorUI.group_0_1.add(knowledgeCollectorUI.ringRight_4)
    knowledgeCollectorUI.group_0_1.add(knowledgeCollectorUI.ringRight_5)
    knowledgeCollectorUI.group_0_1.add(knowledgeCollectorUI.ringRight_6)

    knowledgeCollectorUI.group_0.position.set(0.7, 0, 0)
    knowledgeCollectorUI.group_0_1.position.set(0, 0, 0)
    knowledgeCollectorUI.group_1.position.set(0, 0, 0)
    knowledgeCollectorUI.group_1_1.position.set(0, 0, 0.4)
    knowledgeCollectorUI.group_1_2.position.set(0, 0, -0.4)
    knowledgeCollectorUI.group_2.position.set(0, -0.7, 0)
    knowledgeCollectorUI.group_2_1.position.set(0, 0, 0.4)
    knowledgeCollectorUI.group_2_2.position.set(0, 0, -0.4)
    knowledgeCollectorUI.group_3.position.set(0, -1.4, 0)
    knowledgeCollectorUI.group_3_1.position.set(0, 0, 0.4)
    knowledgeCollectorUI.group_3_2.position.set(0, 0, -0.4)
    knowledgeCollectorUI.group_0.rotation.set(-Math.PI * 0.5, Math.PI * 0.5, 0)
    knowledgeCollectorUI.group_0_1.rotation.set(0 , 0, 0)
    knowledgeCollectorUI.group_1.rotation.set(0, 0, 0)
    knowledgeCollectorUI.group_1_1.rotation.set(0, 0, 0)
    knowledgeCollectorUI.group_1_2.rotation.set(0, Math.PI, 0)
    knowledgeCollectorUI.group_2.rotation.set(0, 0, 0)
    knowledgeCollectorUI.group_2_1.rotation.set(0, 0, 0)
    knowledgeCollectorUI.group_2_2.rotation.set(0, Math.PI, 0)
    knowledgeCollectorUI.group_3.rotation.set(0, 0, 0)
    knowledgeCollectorUI.group_3_1.rotation.set(0, 0, 0)
    knowledgeCollectorUI.group_3_2.rotation.set(0, Math.PI, 0)

    knowledgeCollectorUI.groupResetPosition_0 = knowledgeCollectorUI.group_0.position.clone()
    knowledgeCollectorUI.groupResetRotation_1 = knowledgeCollectorUI.group_1.rotation.clone()
    knowledgeCollectorUI.groupResetRotation_2 = knowledgeCollectorUI.group_2.rotation.clone()
    knowledgeCollectorUI.groupResetRotation_3 = knowledgeCollectorUI.group_3.rotation.clone()

    knowledgeCollector.group_1.add(knowledgeCollectorUI.group_0, knowledgeCollectorUI.group_0_1)
}








export
{
    generateKnowledgeCollectorUIGroupHierarchy
}