import {knowledgeCollector} from "../knowledgeCollector/knowledgeCollector"
import {resourceInterface} from "./resourceInterface"
import {firstPersonGroup} from "../../script"









const coordinateTransformation = () =>
{
    resourceInterface.deltaWorld.subVectors(knowledgeCollector.group_6.position, firstPersonGroup.position)
    
    resourceInterface.distance = Math.sqrt(Math.pow(resourceInterface.deltaWorld.x, 2) + Math.pow(resourceInterface.deltaWorld.z, 2))
    resourceInterface.rotationAngel = Math.acos(resourceInterface.deltaWorld.x / resourceInterface.distance) - Math.PI * 0.5
    
    resourceInterface.deltaLocal.x = resourceInterface.deltaWorld.x * Math.cos(firstPersonGroup.rotation.y) - resourceInterface.deltaWorld.z * Math.sin(firstPersonGroup.rotation.y)
    resourceInterface.deltaLocal.z = resourceInterface.deltaWorld.x * Math.sin(firstPersonGroup.rotation.y) + resourceInterface.deltaWorld.z * Math.cos( firstPersonGroup.rotation.y)
    resourceInterface.deltaLocal.y = resourceInterface.deltaWorld.y
}









export
{
    coordinateTransformation
}














