import {activateKnowledgeCollectorUIAnimationLoop} from "../knowledgeCollectorUI/activateKnowledgeCollectorUIAnimationLoop.js"
import {visibilityKnowledgeCollectorUIRingSystem} from "../knowledgeCollectorUI/visibilityKnowledgeCollectorUIRingSystem.js"
import {initializeActivateKnowledgeCollectorUI} from "../knowledgeCollectorUI/initializeActivateKnowledgeCollectorUI.js"
import {knowledgeCollector} from "./knowledgeCollector"
import {firstPersonLookAroundObj} from "../firstPerson/firstPersonObject"
import {firstPersonCam} from "../../script"
import {MathUtils} from "three"







const hoverOutAnimationLoop = () =>
{
    knowledgeCollector.elapsedTime_1 = knowledgeCollector.clock.getElapsedTime()

    if(knowledgeCollector.elapsedTime_1 < 2)
    {
        firstPersonCam.rotation.x = firstPersonLookAroundObj.currentCamRotationAngelAroundX - MathUtils.smoothstep(knowledgeCollector.elapsedTime_1, 0, 2) * firstPersonLookAroundObj.currentCamRotationAngelAroundX

        knowledgeCollector.kappa = 0.6 + MathUtils.smoothstep(knowledgeCollector.elapsedTime_1, 0, 2) * 0.8
        knowledgeCollector.theta = MathUtils.smoothstep(0.11 + Math.pow(knowledgeCollector.elapsedTime_1, 2), 0, 4.11) * 2 * Math.PI
        knowledgeCollector.radius = knowledgeCollector.kappa /  Math.sqrt(knowledgeCollector.theta)
        knowledgeCollector.y = knowledgeCollector.groupInitialPosition_5.y + MathUtils.smoothstep(knowledgeCollector.elapsedTime_1, 0, 2) * 0.6
        knowledgeCollector.cylindrical.set(knowledgeCollector.radius, knowledgeCollector.theta, knowledgeCollector.y )
        knowledgeCollector.group_5.position.setFromCylindrical(knowledgeCollector.cylindrical)
        
        knowledgeCollector.group_1.rotation.y = knowledgeCollector.groupInitialRotation_1.y - MathUtils.smoothstep(knowledgeCollector.elapsedTime_1, 0, 2) * 3 * Math.PI
        knowledgeCollector.group_2.rotation.x = knowledgeCollector.groupInitialRotation_2.x - MathUtils.smoothstep(knowledgeCollector.elapsedTime_1, 0, 2) * 0.08 * Math.PI
    }

    if(knowledgeCollector.elapsedTime_1 > 0.8 && knowledgeCollector.elapsedTime_1 < 2)
    {
        knowledgeCollector.elapsedTime_2 = knowledgeCollector.elapsedTime_1 - 0.8

        knowledgeCollector.group_3.rotation.y = knowledgeCollector.groupInitialRotation_3.y + MathUtils.smoothstep(knowledgeCollector.elapsedTime_2, 0, 1.2) * 1.6 * Math.PI
    }

    if(knowledgeCollector.elapsedTime_1 > 1.6 && knowledgeCollector.elapsedTime_1 < 2)
    {
        knowledgeCollector.elapsedTime_3 = knowledgeCollector.elapsedTime_1 - 1.6

        knowledgeCollector.group_4.rotation.z = knowledgeCollector.groupInitialRotation_4.z + MathUtils.smoothstep(knowledgeCollector.elapsedTime_3, 0, 0.4) * Math.PI
        knowledgeCollector.group_5.rotation.x = knowledgeCollector.groupInitialRotation_5.x - MathUtils.smoothstep(knowledgeCollector.elapsedTime_3, 0, 0.4) * Math.PI
    }

    if(knowledgeCollector.elapsedTime_1 > 1.75 && knowledgeCollector.elapsedTime_1 < 2.45)
    {
        knowledgeCollector.elapsedTime_4 = knowledgeCollector.elapsedTime_1 - 1.75

        knowledgeCollector.previousSmootherstep = knowledgeCollector.currentSmootherstep
        knowledgeCollector.currentSmootherstep = MathUtils.smootherstep(knowledgeCollector.elapsedTime_4, 0, 0.7)
        knowledgeCollector.deltaSmootherstep = (knowledgeCollector.currentSmootherstep - knowledgeCollector.previousSmootherstep) * 1

        knowledgeCollector.wing_1_1.translateZ(knowledgeCollector.deltaSmootherstep)
        knowledgeCollector.wing_1_2.translateZ(knowledgeCollector.deltaSmootherstep)
        knowledgeCollector.wing_2_1.translateZ(knowledgeCollector.deltaSmootherstep)
        knowledgeCollector.wing_2_2.translateZ(knowledgeCollector.deltaSmootherstep)
        knowledgeCollector.wing_3_1.translateZ(knowledgeCollector.deltaSmootherstep)
        knowledgeCollector.wing_3_2.translateZ(knowledgeCollector.deltaSmootherstep)
        knowledgeCollector.wing_4_1.translateZ(knowledgeCollector.deltaSmootherstep)
        knowledgeCollector.wing_4_2.translateZ(knowledgeCollector.deltaSmootherstep)
        knowledgeCollector.wing_5_1.translateZ(knowledgeCollector.deltaSmootherstep)
        knowledgeCollector.wing_5_2.translateZ(knowledgeCollector.deltaSmootherstep)
        knowledgeCollector.wing_6_1.translateZ(knowledgeCollector.deltaSmootherstep)
        knowledgeCollector.wing_6_2.translateZ(knowledgeCollector.deltaSmootherstep)

        knowledgeCollector.wing_1_1.position.y = knowledgeCollector.wingInitialPosition_1_1.y + MathUtils.smootherstep(knowledgeCollector.elapsedTime_4, 0, 0.7) * 2
        knowledgeCollector.wing_1_2.position.y = knowledgeCollector.wingInitialPosition_1_2.y - MathUtils.smootherstep(knowledgeCollector.elapsedTime_4, 0, 0.7) * 2
        knowledgeCollector.wing_2_1.position.y = knowledgeCollector.wingInitialPosition_2_1.y + MathUtils.smootherstep(knowledgeCollector.elapsedTime_4, 0, 0.7) * 2
        knowledgeCollector.wing_2_2.position.y = knowledgeCollector.wingInitialPosition_2_2.y - MathUtils.smootherstep(knowledgeCollector.elapsedTime_4, 0, 0.7) * 2
        knowledgeCollector.wing_3_1.position.y = knowledgeCollector.wingInitialPosition_3_1.y + MathUtils.smootherstep(knowledgeCollector.elapsedTime_4, 0, 0.7) * 2
        knowledgeCollector.wing_3_2.position.y = knowledgeCollector.wingInitialPosition_3_2.y - MathUtils.smootherstep(knowledgeCollector.elapsedTime_4, 0, 0.7) * 2
        knowledgeCollector.wing_4_1.position.y = knowledgeCollector.wingInitialPosition_4_1.y + MathUtils.smootherstep(knowledgeCollector.elapsedTime_4, 0, 0.7) * 2
        knowledgeCollector.wing_4_2.position.y = knowledgeCollector.wingInitialPosition_4_2.y - MathUtils.smootherstep(knowledgeCollector.elapsedTime_4, 0, 0.7) * 2
        knowledgeCollector.wing_5_1.position.y = knowledgeCollector.wingInitialPosition_5_1.y + MathUtils.smootherstep(knowledgeCollector.elapsedTime_4, 0, 0.7) * 2
        knowledgeCollector.wing_5_2.position.y = knowledgeCollector.wingInitialPosition_5_2.y - MathUtils.smootherstep(knowledgeCollector.elapsedTime_4, 0, 0.7) * 2
        knowledgeCollector.wing_6_1.position.y = knowledgeCollector.wingInitialPosition_6_1.y + MathUtils.smootherstep(knowledgeCollector.elapsedTime_4, 0, 0.7) * 2
        knowledgeCollector.wing_6_2.position.y = knowledgeCollector.wingInitialPosition_6_2.y - MathUtils.smootherstep(knowledgeCollector.elapsedTime_4, 0, 0.7) * 2

        knowledgeCollector.projector_1.position.y = knowledgeCollector.projectorIntialPosition_1.y + MathUtils.smootherstep(knowledgeCollector.elapsedTime_4, 0, 0.7) * 2.25
        knowledgeCollector.projector_2.position.y = knowledgeCollector.projectorIntialPosition_2.y - MathUtils.smootherstep(knowledgeCollector.elapsedTime_4, 0, 0.7) * 2.25
    }

    if(knowledgeCollector.elapsedTime_1 > 2.45)
    {
        //set exact angel
        knowledgeCollector.group_2.rotation.x = knowledgeCollector.groupInitialRotation_2.x - 0.08 * Math.PI 

        knowledgeCollector.animationProtectionShield = false
        knowledgeCollector.cancelAnimationFrame = true
        knowledgeCollector.clock.stop()
        
        visibilityKnowledgeCollectorUIRingSystem()
        initializeActivateKnowledgeCollectorUI()
        activateKnowledgeCollectorUIAnimationLoop()
    }

    knowledgeCollector.animationFrame = requestAnimationFrame(hoverOutAnimationLoop)
    if(knowledgeCollector.cancelAnimationFrame) window.cancelAnimationFrame(knowledgeCollector.animationFrame)
}









export
{
    hoverOutAnimationLoop
}