import {teleporter} from "./teleporter.js"









const teleporterAnimationLoop = () =>
{
    teleporter.elapsedTime_1 = teleporter.clock.getElapsedTime()
    if(teleporter.elapsedTime_1 > 1)
    {
        teleporter.cancelAnimationFrame = true
        teleporter.clock.stop()
        console.log("stop")
    }
    
    teleporter.requestAnimationFrame = window.requestAnimationFrame(teleporterAnimationLoop)
    if(teleporter.cancelAnimationFrame) cancelAnimationFrame(teleporter.requestAnimationFrame)
}









export
{
    teleporterAnimationLoop
}