import {firstPersonMovementObj, firstPersonLookAroundObj} from "../firstPerson/firstPersonObject.js"
import {knowledgeCollector} from "../knowledgeCollector/knowledgeCollector"
import {resourceInterface} from "./resourceInterface"
import {MathUtils} from "three"









const transferAnimationLoop = () =>
{
    resourceInterface.elapsedTime_1 = resourceInterface.clock.getElapsedTime()

    if(resourceInterface.elapsedTime_1 < 1.2)
    {
        knowledgeCollector.previousSmootherstep = knowledgeCollector.currentSmootherstep
        knowledgeCollector.currentSmootherstep = MathUtils.smootherstep(resourceInterface.elapsedTime_1, 0, 1.2)
        knowledgeCollector.deltaSmootherstep = (knowledgeCollector.currentSmootherstep - knowledgeCollector.previousSmootherstep) * 1

        knowledgeCollector.wing_1_1.translateZ(-knowledgeCollector.deltaSmootherstep)
        knowledgeCollector.wing_1_2.translateZ(-knowledgeCollector.deltaSmootherstep)
        knowledgeCollector.wing_2_1.translateZ(-knowledgeCollector.deltaSmootherstep)
        knowledgeCollector.wing_2_2.translateZ(-knowledgeCollector.deltaSmootherstep)
        knowledgeCollector.wing_3_1.translateZ(-knowledgeCollector.deltaSmootherstep)
        knowledgeCollector.wing_3_2.translateZ(-knowledgeCollector.deltaSmootherstep)
        knowledgeCollector.wing_4_1.translateZ(-knowledgeCollector.deltaSmootherstep)
        knowledgeCollector.wing_4_2.translateZ(-knowledgeCollector.deltaSmootherstep)
        knowledgeCollector.wing_5_1.translateZ(-knowledgeCollector.deltaSmootherstep)
        knowledgeCollector.wing_5_2.translateZ(-knowledgeCollector.deltaSmootherstep)
        knowledgeCollector.wing_6_1.translateZ(-knowledgeCollector.deltaSmootherstep)
        knowledgeCollector.wing_6_2.translateZ(-knowledgeCollector.deltaSmootherstep)
    }

    if(resourceInterface.elapsedTime_1 > 0.4 && resourceInterface.elapsedTime_1 < 1.2)
    {
        resourceInterface.elapsedTime_2 = resourceInterface.elapsedTime_1 - 0.4

        knowledgeCollector.wing_1_1.position.y = knowledgeCollector.wingInitialPosition_1_1.y - MathUtils.smootherstep(resourceInterface.elapsedTime_2, 0, 0.8) * 2
        knowledgeCollector.wing_1_2.position.y = knowledgeCollector.wingInitialPosition_1_2.y + MathUtils.smootherstep(resourceInterface.elapsedTime_2, 0, 0.8) * 2
        knowledgeCollector.wing_2_1.position.y = knowledgeCollector.wingInitialPosition_2_1.y - MathUtils.smootherstep(resourceInterface.elapsedTime_2, 0, 0.8) * 2
        knowledgeCollector.wing_2_2.position.y = knowledgeCollector.wingInitialPosition_2_2.y + MathUtils.smootherstep(resourceInterface.elapsedTime_2, 0, 0.8) * 2
        knowledgeCollector.wing_3_1.position.y = knowledgeCollector.wingInitialPosition_3_1.y - MathUtils.smootherstep(resourceInterface.elapsedTime_2, 0, 0.8) * 2
        knowledgeCollector.wing_3_2.position.y = knowledgeCollector.wingInitialPosition_3_2.y + MathUtils.smootherstep(resourceInterface.elapsedTime_2, 0, 0.8) * 2
        knowledgeCollector.wing_4_1.position.y = knowledgeCollector.wingInitialPosition_4_1.y - MathUtils.smootherstep(resourceInterface.elapsedTime_2, 0, 0.8) * 2
        knowledgeCollector.wing_4_2.position.y = knowledgeCollector.wingInitialPosition_4_2.y + MathUtils.smootherstep(resourceInterface.elapsedTime_2, 0, 0.8) * 2
        knowledgeCollector.wing_5_1.position.y = knowledgeCollector.wingInitialPosition_5_1.y - MathUtils.smootherstep(resourceInterface.elapsedTime_2, 0, 0.8) * 2
        knowledgeCollector.wing_5_2.position.y = knowledgeCollector.wingInitialPosition_5_2.y + MathUtils.smootherstep(resourceInterface.elapsedTime_2, 0, 0.8) * 2
        knowledgeCollector.wing_6_1.position.y = knowledgeCollector.wingInitialPosition_6_1.y - MathUtils.smootherstep(resourceInterface.elapsedTime_2, 0, 0.8) * 2
        knowledgeCollector.wing_6_2.position.y = knowledgeCollector.wingInitialPosition_6_2.y + MathUtils.smootherstep(resourceInterface.elapsedTime_2, 0, 0.8) * 2

        knowledgeCollector.projector_1.position.y = knowledgeCollector.projectorIntialPosition_1.y - MathUtils.smootherstep(resourceInterface.elapsedTime_2, 0, 0.8) * 2
        knowledgeCollector.projector_2.position.y = knowledgeCollector.projectorIntialPosition_2.y + MathUtils.smootherstep(resourceInterface.elapsedTime_2, 0, 0.8) * 2
    }

    if(resourceInterface.elapsedTime_1 > 0.9 && resourceInterface.elapsedTime_1 < 1.7)
    {
        resourceInterface.elapsedTime_3 = resourceInterface.elapsedTime_1 - 0.9

        knowledgeCollector.group_1.rotation.x = knowledgeCollector.groupInitialRotation_1.x - MathUtils.smoothstep(resourceInterface.elapsedTime_3, 0, 0.8) * 1.05 * Math.PI
        knowledgeCollector.group_2.rotation.y = knowledgeCollector.groupInitialRotation_2.y - MathUtils.smoothstep(resourceInterface.elapsedTime_3, 0, 0.8) * Math.PI 
        knowledgeCollector.group_3.rotation.y = knowledgeCollector.groupInitialRotation_3.y + MathUtils.smoothstep(resourceInterface.elapsedTime_3, 0, 0.8) * (Math.PI * 0.5 + resourceInterface.rotationAngel)
    }

    if(resourceInterface.elapsedTime_1 > 0.9 && resourceInterface.elapsedTime_1 < 3.2)
    {
        resourceInterface.elapsedTime_4 = resourceInterface.elapsedTime_1 - 0.2

        knowledgeCollector.group_1.rotation.y = knowledgeCollector.groupInitialRotation_1.y - MathUtils.smoothstep(resourceInterface.elapsedTime_4, 0, 2.3) * 3 * Math.PI
    }

    if(resourceInterface.elapsedTime_1 > 1.6 && resourceInterface.elapsedTime_1 < 2.4)
    {
        resourceInterface.elapsedTime_5 = resourceInterface.elapsedTime_1 - 1.6

        knowledgeCollector.group_6.position.x = knowledgeCollector.groupInitialPosition_6.x - MathUtils.smoothstep(resourceInterface.elapsedTime_5, 0, 0.8) * knowledgeCollector.groupInitialPosition_6.x
        knowledgeCollector.group_6.position.y = knowledgeCollector.groupInitialPosition_6.y - MathUtils.smoothstep(resourceInterface.elapsedTime_5, 0, 0.8) * 0.8
        knowledgeCollector.group_6.position.z = knowledgeCollector.groupInitialPosition_6.z - MathUtils.smoothstep(resourceInterface.elapsedTime_5, 0, 0.8) * 1.1 * knowledgeCollector.groupInitialPosition_6.z
    }

    if(resourceInterface.elapsedTime_1 > 2.4)
    {
        firstPersonMovementObj.freeze = false
        firstPersonLookAroundObj.freeze = false
        resourceInterface.transferSuccessful = true
        resourceInterface.cancelAnimationFrame = true

        resourceInterface.clock.stop()
    }

    resourceInterface.animationFrame = requestAnimationFrame(transferAnimationLoop)
    if(resourceInterface.cancelAnimationFrame) window.cancelAnimationFrame(resourceInterface.animationFrame)
} 









export
{
    transferAnimationLoop
}



















window.addEventListener("keydown", (Event) =>
{
    if(Event.key === "g")
    {
        console.log(resourceInterface.rotationAngel)
    }
})