//const material = new MeshBasicMaterial({color: 0xcccccc})
//const material_1 = new MeshStandardMaterial()
const materialUpdate = (scene) =>//!material side issu wird geloest wenn das material nicht exportiert wird folglich werden auch keine gruppen exportiert, dann kann die material side def ueber meshAnalyses geschaehen
{
    scene.traverse((child) =>
    {
        if( child instanceof Mesh && child.material instanceof MeshStandardMaterial && child.name === "wing_1_1")//alle anderen wings glecihes material (asu export) deshalb nur einmal zuweisen
        {
            child.material.side = DoubleSide
        }
        else if(child instanceof Mesh && child.material instanceof MeshStandardMaterial)
        {
            child.material.side = FrontSide
            //child.material = material_1 
        }
    })
}









