import {determineClosestCollectedNode} from "./determineClosestCollectedNode.js"
import {knowledgeCollector} from "../knowledgeCollector/knowledgeCollector.js"
import {setCurrentContentCarrier} from "./setCurrentContentCarrier.js"
import {initializeAnimationLoop} from "./initializeAnimationLoop.js"
import {gesturesSwipeDirection} from "./gesturesSwipeDirection.js"
import {knowledgeCollectorUI} from "./knowledgeCollectorUI.js"
import {currentCoordinates} from "./currentCoordinates.js"
import {setPreviousLevel} from "./setPreviousLevel.js"
import {startCoordinates} from "./startCoordinates.js"
import {setPreviousNode} from "./setPreviousNode.js"
import {setCurrentLevel} from "./setCurrentLevel.js"
import {setCurrentNode} from "./setCurrentNode.js"
import {addCurrentNode} from "./addCurrentNode.js"
import {delta} from "./delta.js"









window.addEventListener("pointerdown", (Event) =>
{
    if(Event.button === 0 && !knowledgeCollector.animationProtectionShield && knowledgeCollectorUI.enabled && knowledgeCollectorUI.controlPermitted)
    {
        startCoordinates(Event)
    }
})









window.addEventListener("pointermove", (Event) =>
{
    if(!knowledgeCollector.animationProtectionShield && knowledgeCollectorUI.enabled && knowledgeCollectorUI.controlPermitted)
    {
        currentCoordinates(Event)
    }
})









window.addEventListener("pointerup", (Event) =>
{
    if(Event.button === 0 && !knowledgeCollector.animationProtectionShield && knowledgeCollectorUI.enabled && knowledgeCollectorUI.controlPermitted)
    {
        delta()
        gesturesSwipeDirection()
        determineClosestCollectedNode()
        
        if(knowledgeCollectorUI.graph.closestCollectedNode !== null)
        {
            setPreviousNode()
            setCurrentNode()
            setPreviousLevel()
            setCurrentLevel()
            setCurrentContentCarrier()
            addCurrentNode()
            initializeAnimationLoop()
        } 
    }
})