import {knowledgeCollector} from "../knowledgeCollector/knowledgeCollector"
import {resourceInterface} from "../resourceInterface/resourceInterface"
import {firstPersonGroup} from "../../script"









const addKnowledgeCollectorToFirstperson = () =>
{
    firstPersonGroup.add(knowledgeCollector.group_6)
    knowledgeCollector.group_6.position.set(resourceInterface.deltaLocal.x, resourceInterface.deltaLocal.y, resourceInterface.deltaLocal.z)
    knowledgeCollector.group_6.rotation.y = -firstPersonGroup.rotation.y
}









export
{
    addKnowledgeCollectorToFirstperson
}