import {knowledgeCollectorUI} from "./knowledgeCollectorUI"









const addCurrentNode = () =>
{
    //level 1
    if(knowledgeCollectorUI.previousLevel === "1" && knowledgeCollectorUI.currentLevel === "1" && knowledgeCollectorUI.groupRotationState_1 === "1")
    {
        knowledgeCollectorUI.group_1_2.add(knowledgeCollectorUI.graph[knowledgeCollectorUI.graph.currentNode].mesh)
        knowledgeCollectorUI.graph[knowledgeCollectorUI.graph.currentNode].mesh.position.set(0, 0, 0)
    }
    else if(knowledgeCollectorUI.previousLevel === "1" && knowledgeCollectorUI.currentLevel === "1" && knowledgeCollectorUI.groupRotationState_1 === "2")
    {
        knowledgeCollectorUI.group_1_1.add(knowledgeCollectorUI.graph[knowledgeCollectorUI.graph.currentNode].mesh)
        knowledgeCollectorUI.graph[knowledgeCollectorUI.graph.currentNode].mesh.position.set(0, 0, 0)
    }
    //level 1 -> 2
    else if(knowledgeCollectorUI.previousLevel === "1" && knowledgeCollectorUI.currentLevel === "2" && knowledgeCollectorUI.groupRotationState_2 === "1")
    {
        knowledgeCollectorUI.group_2_1.add(knowledgeCollectorUI.graph[knowledgeCollectorUI.graph.currentNode].mesh)
        knowledgeCollectorUI.graph[knowledgeCollectorUI.graph.currentNode].mesh.position.set(0, 0, 0)
    }
    else if(knowledgeCollectorUI.previousLevel === "1" && knowledgeCollectorUI.currentLevel === "2" && knowledgeCollectorUI.groupRotationState_2 === "2")
    {
        knowledgeCollectorUI.group_2_2.add(knowledgeCollectorUI.graph[knowledgeCollectorUI.graph.currentNode].mesh)
        knowledgeCollectorUI.graph[knowledgeCollectorUI.graph.currentNode].mesh.position.set(0, 0, 0)
    }
    //level 2
    else if(knowledgeCollectorUI.previousLevel === "2" && knowledgeCollectorUI.currentLevel === "2" && knowledgeCollectorUI.groupRotationState_2 === "1")
    {
        knowledgeCollectorUI.group_2_2.add(knowledgeCollectorUI.graph[knowledgeCollectorUI.graph.currentNode].mesh)
        knowledgeCollectorUI.graph[knowledgeCollectorUI.graph.currentNode].mesh.position.set(0, 0, 0)
    }
    else if(knowledgeCollectorUI.previousLevel === "2" && knowledgeCollectorUI.currentLevel === "2" && knowledgeCollectorUI.groupRotationState_2 === "2")
    {
        knowledgeCollectorUI.group_2_1.add(knowledgeCollectorUI.graph[knowledgeCollectorUI.graph.currentNode].mesh)
        knowledgeCollectorUI.graph[knowledgeCollectorUI.graph.currentNode].mesh.position.set(0, 0, 0)
    }
    //level 2 -> 3
    else if(knowledgeCollectorUI.previousLevel === "2" && knowledgeCollectorUI.currentLevel === "3" && knowledgeCollectorUI.groupRotationState_3 === "1")
    {
        knowledgeCollectorUI.group_3_1.add(knowledgeCollectorUI.graph[knowledgeCollectorUI.graph.currentNode].mesh)
        knowledgeCollectorUI.graph[knowledgeCollectorUI.graph.currentNode].mesh.position.set(0, 0, 0)
    }
    else if(knowledgeCollectorUI.previousLevel === "2" && knowledgeCollectorUI.currentLevel === "3" && knowledgeCollectorUI.groupRotationState_2 === "2")
    {
        knowledgeCollectorUI.group_3_2.add(knowledgeCollectorUI.graph[knowledgeCollectorUI.graph.currentNode].mesh)
        knowledgeCollectorUI.graph[knowledgeCollectorUI.graph.currentNode].mesh.position.set(0, 0, 0)
    }
    //level 3//implementiert aber keine weiteren knoten in level3, wird nicht ueber nodeTravers aufgerufen
    else if(knowledgeCollectorUI.previousLevel === "3" && knowledgeCollectorUI.currentLevel === "3" && knowledgeCollectorUI.groupRotationState_3 === "1")
    {
        knowledgeCollectorUI.group_3_2.add(knowledgeCollectorUI.graph[knowledgeCollectorUI.graph.currentNode].mesh)
        knowledgeCollectorUI.graph[knowledgeCollectorUI.graph.currentNode].mesh.position.set(0, 0, 0)
    }
    else if(knowledgeCollectorUI.previousLevel === "3" && knowledgeCollectorUI.currentLevel === "3" && knowledgeCollectorUI.groupRotationState_3 === "2")
    {
        knowledgeCollectorUI.group_3_1.add(knowledgeCollectorUI.graph[knowledgeCollectorUI.graph.currentNode].mesh)
        knowledgeCollectorUI.graph[knowledgeCollectorUI.graph.currentNode].mesh.position.set(0, 0, 0)
    }
    //level 3 -> 2
    else if(knowledgeCollectorUI.previousLevel === "3" && knowledgeCollectorUI.currentLevel === "2" && knowledgeCollectorUI.groupRotationState_2 === "1")
    {
        knowledgeCollectorUI.graph[knowledgeCollectorUI.graph.currentNode].mesh.material.opacity = 0//behebt folgenden bug: mesh wird in level 2 eingesetzt und ist sichtbar(opacity 0.7) dann lauft animation scale() waehrend dessen mesh sichtbar dann kommt erst opacity() simultan tranlation() und mesh opacity wird von 0 auf 0.7 hochgefahren
        knowledgeCollectorUI.group_2_1.add(knowledgeCollectorUI.graph[knowledgeCollectorUI.graph.currentNode].mesh)
        knowledgeCollectorUI.graph[knowledgeCollectorUI.graph.currentNode].mesh.position.set(0, 0, 0)
    }
    else if(knowledgeCollectorUI.previousLevel === "3" && knowledgeCollectorUI.currentLevel === "2" && knowledgeCollectorUI.groupRotationState_2 === "2")
    {
        knowledgeCollectorUI.graph[knowledgeCollectorUI.graph.currentNode].mesh.material.opacity = 0//behebt folgenden bug: mesh wird in level 2 eingesetzt und ist sichtbar(opacity 0.7) dann lauft animation scale() waehrend dessen mesh sichtbar dann kommt erst opacity() simultan tranlation() und mesh opacity wird von 0 auf 0.7 hochgefahren
        knowledgeCollectorUI.group_2_2.add(knowledgeCollectorUI.graph[knowledgeCollectorUI.graph.currentNode].mesh)
        knowledgeCollectorUI.graph[knowledgeCollectorUI.graph.currentNode].mesh.position.set(0, 0, 0)
    }
    //level 2 -> 1
    else if(knowledgeCollectorUI.previousLevel === "2" && knowledgeCollectorUI.currentLevel === "1" && knowledgeCollectorUI.groupRotationState_1 === "1")
    {
        knowledgeCollectorUI.group_1_1.add(knowledgeCollectorUI.graph[knowledgeCollectorUI.graph.currentNode].mesh)
        knowledgeCollectorUI.graph[knowledgeCollectorUI.graph.currentNode].mesh.position.set(0, 0, 0)
    }
    else if(knowledgeCollectorUI.previousLevel === "2" && knowledgeCollectorUI.currentLevel === "1" && knowledgeCollectorUI.groupRotationState_1 === "2")
    {
        knowledgeCollectorUI.group_1_2.add(knowledgeCollectorUI.graph[knowledgeCollectorUI.graph.currentNode].mesh)
        knowledgeCollectorUI.graph[knowledgeCollectorUI.graph.currentNode].mesh.position.set(0, 0, 0)
    }
}







export
{
    addCurrentNode
}