import {studyModeObj } from "./studyObject"
import {cameraCollection, orbitControlStudy, scene} from "../../script"
import {firstPersonMovementObj, firstPersonLookAroundObj} from "../firstPerson/firstPersonObject"
import {MathUtils} from "three"



const studyMode = () =>
{
    if(!studyModeObj.active)
    {
        cameraCollection.activeCam = cameraCollection.studyCam
        orbitControlStudy.enabled = true
        studyModeObj.active = true
        firstPersonMovementObj.freeze = true
        firstPersonLookAroundObj.freeze = true

        generateSuzanne()
        contentVisulize()

    }
    else
    {
        cameraCollection.activeCam = cameraCollection.firstPersonCam
        orbitControlStudy.enabled = false
        studyModeObj.active = false
        firstPersonMovementObj.freeze = false
        firstPersonLookAroundObj.freeze = false

        contentDispose()
    }
}

//!test
const generateSuzanne = () =>
{
    studyModeObj.time0.start()
    studyModeObj.cancelAnimationFram = false

    studyModeObj.suzanne.material.opacity = 0
    scene.add(studyModeObj.suzanne)
}


const contentVisulize = () =>
{
    studyModeObj.elapsedTime = studyModeObj.time0.getElapsedTime()

    if(studyModeObj.elapsedTime < 2)
    {
        studyModeObj.suzanne.material.opacity = MathUtils.smoothstep(studyModeObj.elapsedTime, 0, 2) * 1
    }
    else
    {
        studyModeObj.time0.stop()
        studyModeObj.cancelAnimationFram = true
    }

    studyModeObj.animationFrame = requestAnimationFrame(contentVisulize)
    if(studyModeObj.cancelAnimationFram) window.cancelAnimationFrame(studyModeObj.animationFrame)
}


const contentDispose = () =>
{
    scene.remove(studyModeObj.suzanne)
    studyModeObj.suzanne.geometry.dispose()
    studyModeObj.suzanne.material.dispose()
}
//!



window.addEventListener("keydown", (Event) =>
{
    if(Event.key === "3")
    {
        studyMode()
    }
})



