import {knowledgeCollector} from "../knowledgeCollector/knowledgeCollector.js"
import {knowledgeCollectorUI} from "./knowledgeCollectorUI.js"
import {MathUtils} from "three"









const activateKnowledgeCollectorUIAnimationLoop = () =>
{
    knowledgeCollectorUI.elapsedTimeActivateKnowledgeCollectorUI_1 = knowledgeCollectorUI.clockActivateKnowledgeCollectorUI.getElapsedTime()

    if(knowledgeCollectorUI.elapsedTimeActivateKnowledgeCollectorUI_1 < 0.1)
    {
        knowledgeCollectorUI.variableOpacityStep_1 = MathUtils.smoothstep(knowledgeCollectorUI.elapsedTimeActivateKnowledgeCollectorUI_1, 0, 0.1) * 0.6

        knowledgeCollectorUI.ringLeft_1.material.opacity = knowledgeCollectorUI.variableOpacityStep_1
        knowledgeCollectorUI.ringLeft_2.material.opacity = knowledgeCollectorUI.variableOpacityStep_1
        knowledgeCollectorUI.ringLeft_3.material.opacity = knowledgeCollectorUI.variableOpacityStep_1
        knowledgeCollectorUI.ringLeft_4.material.opacity = knowledgeCollectorUI.variableOpacityStep_1
        knowledgeCollectorUI.ringLeft_5.material.opacity = knowledgeCollectorUI.variableOpacityStep_1
        knowledgeCollectorUI.ringLeft_6.material.opacity = knowledgeCollectorUI.variableOpacityStep_1
        
        knowledgeCollectorUI.ringRight_1.material.opacity = knowledgeCollectorUI.variableOpacityStep_1
        knowledgeCollectorUI.ringRight_2.material.opacity = knowledgeCollectorUI.variableOpacityStep_1
        knowledgeCollectorUI.ringRight_3.material.opacity = knowledgeCollectorUI.variableOpacityStep_1
        knowledgeCollectorUI.ringRight_4.material.opacity = knowledgeCollectorUI.variableOpacityStep_1
        knowledgeCollectorUI.ringRight_5.material.opacity = knowledgeCollectorUI.variableOpacityStep_1
        knowledgeCollectorUI.ringRight_6.material.opacity = knowledgeCollectorUI.variableOpacityStep_1

        //!console.log(knowledgeCollectorUI.elapsedTimeActivateKnowledgeCollectorUI_1)
    }
    else if(knowledgeCollectorUI.elapsedTimeActivateKnowledgeCollectorUI_1 > 0.1 && knowledgeCollectorUI.elapsedTimeActivateKnowledgeCollectorUI_1 < 0.3)
    {
        knowledgeCollectorUI.elapsedTimeActivateKnowledgeCollectorUI_2 = knowledgeCollectorUI.elapsedTimeActivateKnowledgeCollectorUI_1 - 0.1

        knowledgeCollectorUI.variableOpacityStep_2 = 0.2 * Math.sin(Math.PI * (45 / 2) * knowledgeCollectorUI.elapsedTimeActivateKnowledgeCollectorUI_2)

        knowledgeCollectorUI.ringLeft_1.material.opacity = knowledgeCollectorUI.variableOpacityStep_1 + knowledgeCollectorUI.variableOpacityStep_2
        knowledgeCollectorUI.ringLeft_2.material.opacity = knowledgeCollectorUI.variableOpacityStep_1 + knowledgeCollectorUI.variableOpacityStep_2
        knowledgeCollectorUI.ringLeft_3.material.opacity = knowledgeCollectorUI.variableOpacityStep_1 + knowledgeCollectorUI.variableOpacityStep_2
        knowledgeCollectorUI.ringLeft_4.material.opacity = knowledgeCollectorUI.variableOpacityStep_1 + knowledgeCollectorUI.variableOpacityStep_2
        knowledgeCollectorUI.ringLeft_5.material.opacity = knowledgeCollectorUI.variableOpacityStep_1 + knowledgeCollectorUI.variableOpacityStep_2
        knowledgeCollectorUI.ringLeft_6.material.opacity = knowledgeCollectorUI.variableOpacityStep_1 + knowledgeCollectorUI.variableOpacityStep_2
        
        knowledgeCollectorUI.ringRight_1.material.opacity = knowledgeCollectorUI.variableOpacityStep_1 + knowledgeCollectorUI.variableOpacityStep_2
        knowledgeCollectorUI.ringRight_2.material.opacity = knowledgeCollectorUI.variableOpacityStep_1 + knowledgeCollectorUI.variableOpacityStep_2
        knowledgeCollectorUI.ringRight_3.material.opacity = knowledgeCollectorUI.variableOpacityStep_1 + knowledgeCollectorUI.variableOpacityStep_2
        knowledgeCollectorUI.ringRight_4.material.opacity = knowledgeCollectorUI.variableOpacityStep_1 + knowledgeCollectorUI.variableOpacityStep_2
        knowledgeCollectorUI.ringRight_5.material.opacity = knowledgeCollectorUI.variableOpacityStep_1 + knowledgeCollectorUI.variableOpacityStep_2
        knowledgeCollectorUI.ringRight_6.material.opacity = knowledgeCollectorUI.variableOpacityStep_1 + knowledgeCollectorUI.variableOpacityStep_2
    }
    else if(!knowledgeCollectorUI.oneTimeIteration)
    {
        for(let i = 1 ; i <= 6 ; i++)
        {
            //set exact value
            knowledgeCollectorUI[`ringLeft_${i}`].material.opacity = 0.8
            knowledgeCollectorUI[`ringRight_${i}`].material.opacity = 0.8
        }

        knowledgeCollectorUI.oneTimeIteration = true
    }

    if(knowledgeCollectorUI.elapsedTimeActivateKnowledgeCollectorUI_1 < 0.2)
    {
        knowledgeCollectorUI.variableScaleStep_1 = MathUtils.smoothstep(knowledgeCollectorUI.elapsedTimeActivateKnowledgeCollectorUI_1, 0, 0.2) * 0.7

        knowledgeCollectorUI.ringLeft_1.scale.x = knowledgeCollectorUI.ringLeftInitialScale_1.x + knowledgeCollectorUI.variableScaleStep_1
        knowledgeCollectorUI.ringLeft_1.scale.y = knowledgeCollectorUI.ringLeftInitialScale_1.y + knowledgeCollectorUI.variableScaleStep_1
        knowledgeCollectorUI.ringLeft_2.scale.x = knowledgeCollectorUI.ringLeftInitialScale_2.x + knowledgeCollectorUI.variableScaleStep_1
        knowledgeCollectorUI.ringLeft_2.scale.y = knowledgeCollectorUI.ringLeftInitialScale_2.y + knowledgeCollectorUI.variableScaleStep_1
        knowledgeCollectorUI.ringLeft_3.scale.x = knowledgeCollectorUI.ringLeftInitialScale_3.x + knowledgeCollectorUI.variableScaleStep_1
        knowledgeCollectorUI.ringLeft_3.scale.y = knowledgeCollectorUI.ringLeftInitialScale_3.y + knowledgeCollectorUI.variableScaleStep_1
        knowledgeCollectorUI.ringLeft_4.scale.x = knowledgeCollectorUI.ringLeftInitialScale_4.x + knowledgeCollectorUI.variableScaleStep_1
        knowledgeCollectorUI.ringLeft_4.scale.y = knowledgeCollectorUI.ringLeftInitialScale_4.y + knowledgeCollectorUI.variableScaleStep_1
        knowledgeCollectorUI.ringLeft_5.scale.x = knowledgeCollectorUI.ringLeftInitialScale_5.x + knowledgeCollectorUI.variableScaleStep_1
        knowledgeCollectorUI.ringLeft_5.scale.y = knowledgeCollectorUI.ringLeftInitialScale_5.y + knowledgeCollectorUI.variableScaleStep_1
        knowledgeCollectorUI.ringLeft_6.scale.x = knowledgeCollectorUI.ringLeftInitialScale_6.x + knowledgeCollectorUI.variableScaleStep_1
        knowledgeCollectorUI.ringLeft_6.scale.y = knowledgeCollectorUI.ringLeftInitialScale_6.y + knowledgeCollectorUI.variableScaleStep_1

        knowledgeCollectorUI.ringRight_1.scale.x = knowledgeCollectorUI.ringRightInitialScale_1.x + knowledgeCollectorUI.variableScaleStep_1
        knowledgeCollectorUI.ringRight_1.scale.y = knowledgeCollectorUI.ringRightInitialScale_1.y + knowledgeCollectorUI.variableScaleStep_1
        knowledgeCollectorUI.ringRight_2.scale.x = knowledgeCollectorUI.ringRightInitialScale_2.x + knowledgeCollectorUI.variableScaleStep_1
        knowledgeCollectorUI.ringRight_2.scale.y = knowledgeCollectorUI.ringRightInitialScale_2.y + knowledgeCollectorUI.variableScaleStep_1
        knowledgeCollectorUI.ringRight_3.scale.x = knowledgeCollectorUI.ringRightInitialScale_3.x + knowledgeCollectorUI.variableScaleStep_1
        knowledgeCollectorUI.ringRight_3.scale.y = knowledgeCollectorUI.ringRightInitialScale_3.y + knowledgeCollectorUI.variableScaleStep_1
        knowledgeCollectorUI.ringRight_4.scale.x = knowledgeCollectorUI.ringRightInitialScale_4.x + knowledgeCollectorUI.variableScaleStep_1
        knowledgeCollectorUI.ringRight_4.scale.y = knowledgeCollectorUI.ringRightInitialScale_4.y + knowledgeCollectorUI.variableScaleStep_1
        knowledgeCollectorUI.ringRight_5.scale.x = knowledgeCollectorUI.ringRightInitialScale_5.x + knowledgeCollectorUI.variableScaleStep_1
        knowledgeCollectorUI.ringRight_5.scale.y = knowledgeCollectorUI.ringRightInitialScale_5.y + knowledgeCollectorUI.variableScaleStep_1
        knowledgeCollectorUI.ringRight_6.scale.x = knowledgeCollectorUI.ringRightInitialScale_6.x + knowledgeCollectorUI.variableScaleStep_1
        knowledgeCollectorUI.ringRight_6.scale.y = knowledgeCollectorUI.ringRightInitialScale_6.y + knowledgeCollectorUI.variableScaleStep_1
    }
    else if(knowledgeCollectorUI.elapsedTimeActivateKnowledgeCollectorUI_1 > 0.2 && knowledgeCollectorUI.elapsedTimeActivateKnowledgeCollectorUI_1 < 0.4)
    {
        knowledgeCollectorUI.elapsedTimeActivateKnowledgeCollectorUI_3 = knowledgeCollectorUI.elapsedTimeActivateKnowledgeCollectorUI_1 - 0.2

        knowledgeCollectorUI.variableRotationStep_1 = 0.1 * Math.PI * Math.sin(4 * Math.PI / 0.2 * knowledgeCollectorUI.elapsedTimeActivateKnowledgeCollectorUI_3) 

        knowledgeCollectorUI.ringLeft_1.rotation.z = knowledgeCollectorUI.ringLeftInitialRotation_1.z + knowledgeCollectorUI.variableRotationStep_1
        knowledgeCollectorUI.ringLeft_2.rotation.z = knowledgeCollectorUI.ringLeftInitialRotation_2.z - knowledgeCollectorUI.variableRotationStep_1
        knowledgeCollectorUI.ringLeft_3.rotation.z = knowledgeCollectorUI.ringLeftInitialRotation_3.z + knowledgeCollectorUI.variableRotationStep_1
        knowledgeCollectorUI.ringLeft_4.rotation.z = knowledgeCollectorUI.ringLeftInitialRotation_4.z - knowledgeCollectorUI.variableRotationStep_1
        knowledgeCollectorUI.ringLeft_5.rotation.z = knowledgeCollectorUI.ringLeftInitialRotation_5.z + knowledgeCollectorUI.variableRotationStep_1
        knowledgeCollectorUI.ringLeft_6.rotation.z = knowledgeCollectorUI.ringLeftInitialRotation_6.z - knowledgeCollectorUI.variableRotationStep_1
        
        knowledgeCollectorUI.ringRight_1.rotation.z = knowledgeCollectorUI.ringRightInitialRotation_1.z + knowledgeCollectorUI.variableRotationStep_1
        knowledgeCollectorUI.ringRight_2.rotation.z = knowledgeCollectorUI.ringRightInitialRotation_2.z - knowledgeCollectorUI.variableRotationStep_1
        knowledgeCollectorUI.ringRight_3.rotation.z = knowledgeCollectorUI.ringRightInitialRotation_3.z + knowledgeCollectorUI.variableRotationStep_1
        knowledgeCollectorUI.ringRight_4.rotation.z = knowledgeCollectorUI.ringRightInitialRotation_4.z - knowledgeCollectorUI.variableRotationStep_1
        knowledgeCollectorUI.ringRight_5.rotation.z = knowledgeCollectorUI.ringRightInitialRotation_5.z + knowledgeCollectorUI.variableRotationStep_1
        knowledgeCollectorUI.ringRight_6.rotation.z = knowledgeCollectorUI.ringRightInitialRotation_6.z - knowledgeCollectorUI.variableRotationStep_1
    }

    if(knowledgeCollectorUI.elapsedTimeActivateKnowledgeCollectorUI_1 > 0.5 && knowledgeCollectorUI.elapsedTimeActivateKnowledgeCollectorUI_1 < 0.6)
    {
        knowledgeCollectorUI.elapsedTimeActivateKnowledgeCollectorUI_4 = knowledgeCollectorUI.elapsedTimeActivateKnowledgeCollectorUI_1 - 0.5
        
        knowledgeCollectorUI.variablePositionStep_1 = MathUtils.smoothstep(knowledgeCollectorUI.elapsedTimeActivateKnowledgeCollectorUI_4, 0, 0.1) * 0.05
        knowledgeCollectorUI.variablePositionStep_2 = MathUtils.smoothstep(knowledgeCollectorUI.elapsedTimeActivateKnowledgeCollectorUI_4, 0, 0.1) * 0.1
        knowledgeCollectorUI.variablePositionStep_3 = MathUtils.smoothstep(knowledgeCollectorUI.elapsedTimeActivateKnowledgeCollectorUI_4, 0, 0.1) * 0.21
        knowledgeCollectorUI.variablePositionStep_4 = MathUtils.smoothstep(knowledgeCollectorUI.elapsedTimeActivateKnowledgeCollectorUI_4, 0, 0.1) * 0.25
        knowledgeCollectorUI.variablePositionStep_5 = MathUtils.smoothstep(knowledgeCollectorUI.elapsedTimeActivateKnowledgeCollectorUI_4, 0, 0.1) * 0.29

        knowledgeCollectorUI.ringLeft_2.position.y = knowledgeCollectorUI.ringLeftInitialPosition_2.y - knowledgeCollectorUI.variablePositionStep_1
        knowledgeCollectorUI.ringLeft_3.position.y = knowledgeCollectorUI.ringLeftInitialPosition_3.y - knowledgeCollectorUI.variablePositionStep_2
        knowledgeCollectorUI.ringLeft_4.position.y = knowledgeCollectorUI.ringLeftInitialPosition_4.y - knowledgeCollectorUI.variablePositionStep_3
        knowledgeCollectorUI.ringLeft_5.position.y = knowledgeCollectorUI.ringLeftInitialPosition_5.y - knowledgeCollectorUI.variablePositionStep_4
        knowledgeCollectorUI.ringLeft_6.position.y = knowledgeCollectorUI.ringLeftInitialPosition_6.y - knowledgeCollectorUI.variablePositionStep_5
        
        knowledgeCollectorUI.ringRight_2.position.y = knowledgeCollectorUI.ringRightInitialPosition_2.y + knowledgeCollectorUI.variablePositionStep_1
        knowledgeCollectorUI.ringRight_3.position.y = knowledgeCollectorUI.ringRightInitialPosition_3.y + knowledgeCollectorUI.variablePositionStep_2
        knowledgeCollectorUI.ringRight_4.position.y = knowledgeCollectorUI.ringRightInitialPosition_4.y + knowledgeCollectorUI.variablePositionStep_3
        knowledgeCollectorUI.ringRight_5.position.y = knowledgeCollectorUI.ringRightInitialPosition_5.y + knowledgeCollectorUI.variablePositionStep_4
        knowledgeCollectorUI.ringRight_6.position.y = knowledgeCollectorUI.ringRightInitialPosition_6.y + knowledgeCollectorUI.variablePositionStep_5
    }

    if(knowledgeCollectorUI.elapsedTimeActivateKnowledgeCollectorUI_1 > 0.55 && knowledgeCollectorUI.elapsedTimeActivateKnowledgeCollectorUI_1 < 0.65)
    {
        knowledgeCollectorUI.elapsedTimeActivateKnowledgeCollectorUI_5 = knowledgeCollectorUI.elapsedTimeActivateKnowledgeCollectorUI_1 - 0.55

        knowledgeCollectorUI.variableOpacityStep_3 = MathUtils.smoothstep(knowledgeCollectorUI.elapsedTimeActivateKnowledgeCollectorUI_5, 0, 0.1) * 0.8

        knowledgeCollectorUI[knowledgeCollectorUI.currentContentCarrier].material.opacity = knowledgeCollectorUI.variableOpacityStep_3
        knowledgeCollectorUI.graph[knowledgeCollectorUI.graph.currentNode].mesh.material.opacity = knowledgeCollectorUI.variableOpacityStep_3
    }
    else if(knowledgeCollectorUI.elapsedTimeActivateKnowledgeCollectorUI_1 > 0.65)
    {
        //set exact value
        knowledgeCollectorUI[knowledgeCollectorUI.currentContentCarrier].material.opacity = 0.8
        knowledgeCollectorUI.graph[knowledgeCollectorUI.graph.currentNode].mesh.material.opacity = 0.8

        knowledgeCollectorUI.enabled = true
        knowledgeCollector.animationProtectionShield = false
        knowledgeCollectorUI.cancelAnimationFrameActivateKnowledgeCollectorUI = true
        knowledgeCollectorUI.clockActivateKnowledgeCollectorUI.stop()
    }

    knowledgeCollectorUI.requestAnimationFrameActivateKnowledgeCollectorUI = requestAnimationFrame(activateKnowledgeCollectorUIAnimationLoop)
    if(knowledgeCollectorUI.cancelAnimationFrameActivateKnowledgeCollectorUI) window.cancelAnimationFrame(knowledgeCollectorUI.requestAnimationFrameActivateKnowledgeCollectorUI)
}









export
{
    activateKnowledgeCollectorUIAnimationLoop
}