    knowledgeCollectorUI.graph[knowledgeCollectorUI.graph.previousNode].mesh.material.opacity = 0.7//wird erst wieder auf 0.7 gesetzt wenn mesh ring verlassen hat, ansonsten ist er sichtbar wenn er gleich nach opacity() auf 0.7 gesetzt wird wird noch scale() ablaeuft
