import {knowledgeCollectorUI} from "./knowledgeCollectorUI.js"









const delta = () =>
{
    knowledgeCollectorUI.delta.x = knowledgeCollectorUI.currentCoordinates.x - knowledgeCollectorUI.startCoordinates.x
    knowledgeCollectorUI.delta.y = knowledgeCollectorUI.currentCoordinates.y - knowledgeCollectorUI.startCoordinates.y
}









export
{
    delta
}