import {knowledgeCollectorUI} from "./knowledgeCollectorUI"
import {deletePreviousNode} from "./deletePreviousNode.js"
import {MathUtils} from "three"









const rotationAnimationLoop = (group, groupInitialRotation, gropRotationCount) =>
{
    knowledgeCollectorUI.elapsedTimeRotation = knowledgeCollectorUI.clockRotation.getElapsedTime()

    if(knowledgeCollectorUI.elapsedTimeRotation < 0.4)
    {
        knowledgeCollectorUI.variableRotationStep_1 = MathUtils.smoothstep(knowledgeCollectorUI.elapsedTimeRotation, 0, 0.4) * Math.PI * 0.4
        knowledgeCollectorUI.variableRotationStep_2 = MathUtils.smoothstep(knowledgeCollectorUI.elapsedTimeRotation, 0, 0.4) * Math.PI * 0.48
        knowledgeCollectorUI.variableRotationStep_3 = MathUtils.smoothstep(knowledgeCollectorUI.elapsedTimeRotation, 0, 0.4) * Math.PI * 0.56
        knowledgeCollectorUI.variableRotationStep_4 = MathUtils.smoothstep(knowledgeCollectorUI.elapsedTimeRotation, 0, 0.4) * Math.PI * 0.64
        knowledgeCollectorUI.variableRotationStep_5 = MathUtils.smoothstep(knowledgeCollectorUI.elapsedTimeRotation, 0, 0.4) * Math.PI * 0.72
        knowledgeCollectorUI.variableRotationStep_6 = MathUtils.smoothstep(knowledgeCollectorUI.elapsedTimeRotation, 0, 0.4) * Math.PI * 0.80

        knowledgeCollectorUI.ringLeft_1.rotation.z = knowledgeCollectorUI.ringLeftInitialRotation_1.z - knowledgeCollectorUI.variableRotationStep_1 * knowledgeCollectorUI.rotationDirectionFactor
        knowledgeCollectorUI.ringLeft_2.rotation.z = knowledgeCollectorUI.ringLeftInitialRotation_2.z - knowledgeCollectorUI.variableRotationStep_2 * knowledgeCollectorUI.rotationDirectionFactor
        knowledgeCollectorUI.ringLeft_3.rotation.z = knowledgeCollectorUI.ringLeftInitialRotation_3.z - knowledgeCollectorUI.variableRotationStep_3 * knowledgeCollectorUI.rotationDirectionFactor
        knowledgeCollectorUI.ringLeft_4.rotation.z = knowledgeCollectorUI.ringLeftInitialRotation_4.z - knowledgeCollectorUI.variableRotationStep_4 * knowledgeCollectorUI.rotationDirectionFactor
        knowledgeCollectorUI.ringLeft_5.rotation.z = knowledgeCollectorUI.ringLeftInitialRotation_5.z - knowledgeCollectorUI.variableRotationStep_5 * knowledgeCollectorUI.rotationDirectionFactor
        knowledgeCollectorUI.ringLeft_6.rotation.z = knowledgeCollectorUI.ringLeftInitialRotation_6.z - knowledgeCollectorUI.variableRotationStep_6 * knowledgeCollectorUI.rotationDirectionFactor
        knowledgeCollectorUI.ringRight_1.rotation.z = knowledgeCollectorUI.ringRightInitialRotation_1.z - knowledgeCollectorUI.variableRotationStep_1 * knowledgeCollectorUI.rotationDirectionFactor
        knowledgeCollectorUI.ringRight_2.rotation.z = knowledgeCollectorUI.ringRightInitialRotation_2.z - knowledgeCollectorUI.variableRotationStep_2 * knowledgeCollectorUI.rotationDirectionFactor
        knowledgeCollectorUI.ringRight_3.rotation.z = knowledgeCollectorUI.ringRightInitialRotation_3.z - knowledgeCollectorUI.variableRotationStep_3 * knowledgeCollectorUI.rotationDirectionFactor
        knowledgeCollectorUI.ringRight_4.rotation.z = knowledgeCollectorUI.ringRightInitialRotation_4.z - knowledgeCollectorUI.variableRotationStep_4 * knowledgeCollectorUI.rotationDirectionFactor
        knowledgeCollectorUI.ringRight_5.rotation.z = knowledgeCollectorUI.ringRightInitialRotation_5.z - knowledgeCollectorUI.variableRotationStep_5 * knowledgeCollectorUI.rotationDirectionFactor
        knowledgeCollectorUI.ringRight_6.rotation.z = knowledgeCollectorUI.ringRightInitialRotation_6.z - knowledgeCollectorUI.variableRotationStep_6 * knowledgeCollectorUI.rotationDirectionFactor

        knowledgeCollectorUI[group].rotation.y = knowledgeCollectorUI[groupInitialRotation].y + (MathUtils.smoothstep(knowledgeCollectorUI.elapsedTimeRotation,  0, 0.4) * Math.PI * knowledgeCollectorUI.rotationDirectionFactor)
    }

    if(knowledgeCollectorUI.elapsedTimeRotation > 0.4)
    {
        //set exact position
        knowledgeCollectorUI.gesturesSwipeDirection === "right" ? knowledgeCollectorUI[gropRotationCount]++ : knowledgeCollectorUI[gropRotationCount]--
        knowledgeCollectorUI[group].rotation.y = knowledgeCollectorUI[gropRotationCount] * Math.PI

        knowledgeCollectorUI.cancelAnimationFrameRotation = true
        knowledgeCollectorUI.clockRotation.stop()

        deletePreviousNode()
    }

    knowledgeCollectorUI.animationFrameRotation = requestAnimationFrame(() => {rotationAnimationLoop(group, groupInitialRotation, gropRotationCount)})//callbackfunction kann keine argumente annehmen deshalb trick mit annonymen function
    if(knowledgeCollectorUI.cancelAnimationFrameRotation) window.cancelAnimationFrame(knowledgeCollectorUI.animationFrameRotation)
}









export
{
    rotationAnimationLoop
}