import {BufferGeometry, BufferAttribute, PointsMaterial, Points, AdditiveBlending} from "three"
import {resourceInterface} from "./resourceInterface"
import {textures} from "../textures/texturesj"
import {scene} from "../../script"









const generateResourceInterfaceParticleSystem = () =>
{
    const particlesGeometry = new BufferGeometry()
    const positions = new Float32Array(300 * 3)
    const velocitys = new Float32Array(300)
    for(let i = 0 ; i < 300 ; i++)
    {
        const i3 = i * 3
        const y = i % 4 === 0 ? 0 : i % 4 === 1 ? 0.1 : i % 4 === 2 ? 1.1 : 1.2
        const radius = i % 4 === 0 ? 0.6 : i % 4 === 1 ? 0.3 : i % 4 === 2 ? 0.3 : 0.6
        positions[i3 + 0] = radius * Math.sin(i * 0.1) 
        positions[i3 + 1] = y
        positions[i3 + 2] = radius * Math.cos(i * 0.1)
        velocitys[i] = Math.random() + 0.3
    }
    particlesGeometry.setAttribute("position", new BufferAttribute(positions, 3))
    particlesGeometry.setAttribute("velocity", new BufferAttribute(velocitys, 1))
    
    const particlesMaterial = new PointsMaterial()
    particlesMaterial.map = textures.resourceInterfaceParticles
    particlesMaterial.sizeAttenuation = true
    particlesMaterial.size = 0.2
    particlesMaterial.blending = AdditiveBlending
    particlesMaterial.transparent = true
    particlesMaterial.opacity = 1
    particlesMaterial.depthTest = true
    particlesMaterial.depthWrite = false
    
    resourceInterface.particles = new Points(particlesGeometry, particlesMaterial)
    resourceInterface.particles.position.set(-3.42, -2.6, 43.35)
    resourceInterface.particles.visible = false

    scene.add(resourceInterface.particles)
}









export
{
    generateResourceInterfaceParticleSystem
}