import {knowledgeCollectorUI} from "./knowledgeCollectorUI.js"









const collectNodeMesh = (nodeIdLevel2, nodeIdLevel3) =>
{
    if(!knowledgeCollectorUI.nodes[211].collected)
    {
        knowledgeCollectorUI.nodes[211].collected = true
    }
    
    if(!knowledgeCollectorUI.nodes[nodeIdLevel2].collected)
    {
        knowledgeCollectorUI.nodes[nodeIdLevel2].collected = true
        knowledgeCollectorUI.nodes[nodeIdLevel3].collected = true
    }
}









export
{
    collectNodeMesh
}