import {firstPersonMovementObj, firstPersonLookAroundObj} from "../firstPerson/firstPersonObject.js"
import {resetKnowledgeCollectorSegmentsLocation} from "./resetKnowledgeCollectorSegmentsLocation.js"
import {knowledgeCollector} from "./knowledgeCollector.js"
import {MathUtils} from "three"







const hoverBackAnimationLoop = () =>
{
    knowledgeCollector.elapsedTime_1 = knowledgeCollector.clock.getElapsedTime()

    if(knowledgeCollector.elapsedTime_1 < 0.7)
    {
        knowledgeCollector.previousSmootherstep = knowledgeCollector.currentSmootherstep
        knowledgeCollector.currentSmootherstep = MathUtils.smootherstep(knowledgeCollector.elapsedTime_1, 0, 0.7)
        knowledgeCollector.deltaSmootherstep = (knowledgeCollector.currentSmootherstep - knowledgeCollector.previousSmootherstep) * 1

        knowledgeCollector.wing_1_1.translateZ(-knowledgeCollector.deltaSmootherstep)
        knowledgeCollector.wing_1_2.translateZ(-knowledgeCollector.deltaSmootherstep)
        knowledgeCollector.wing_2_1.translateZ(-knowledgeCollector.deltaSmootherstep)
        knowledgeCollector.wing_2_2.translateZ(-knowledgeCollector.deltaSmootherstep)
        knowledgeCollector.wing_3_1.translateZ(-knowledgeCollector.deltaSmootherstep)
        knowledgeCollector.wing_3_2.translateZ(-knowledgeCollector.deltaSmootherstep)
        knowledgeCollector.wing_4_1.translateZ(-knowledgeCollector.deltaSmootherstep)
        knowledgeCollector.wing_4_2.translateZ(-knowledgeCollector.deltaSmootherstep)
        knowledgeCollector.wing_5_1.translateZ(-knowledgeCollector.deltaSmootherstep)
        knowledgeCollector.wing_5_2.translateZ(-knowledgeCollector.deltaSmootherstep)
        knowledgeCollector.wing_6_1.translateZ(-knowledgeCollector.deltaSmootherstep)
        knowledgeCollector.wing_6_2.translateZ(-knowledgeCollector.deltaSmootherstep)
    }

    if(knowledgeCollector.elapsedTime_1 > 0.2 && knowledgeCollector.elapsedTime_1 < 0.8)
    {
        knowledgeCollector.elapsedTime_2 = knowledgeCollector.elapsedTime_1 - 0.2

        knowledgeCollector.wing_1_1.position.y = knowledgeCollector.wingInitialPosition_1_1.y - MathUtils.smootherstep(knowledgeCollector.elapsedTime_2, 0, 0.6) * 2
        knowledgeCollector.wing_1_2.position.y = knowledgeCollector.wingInitialPosition_1_2.y + MathUtils.smootherstep(knowledgeCollector.elapsedTime_2, 0, 0.6) * 2
        knowledgeCollector.wing_2_1.position.y = knowledgeCollector.wingInitialPosition_2_1.y - MathUtils.smootherstep(knowledgeCollector.elapsedTime_2, 0, 0.6) * 2
        knowledgeCollector.wing_2_2.position.y = knowledgeCollector.wingInitialPosition_2_2.y + MathUtils.smootherstep(knowledgeCollector.elapsedTime_2, 0, 0.6) * 2
        knowledgeCollector.wing_3_1.position.y = knowledgeCollector.wingInitialPosition_3_1.y - MathUtils.smootherstep(knowledgeCollector.elapsedTime_2, 0, 0.6) * 2
        knowledgeCollector.wing_3_2.position.y = knowledgeCollector.wingInitialPosition_3_2.y + MathUtils.smootherstep(knowledgeCollector.elapsedTime_2, 0, 0.6) * 2
        knowledgeCollector.wing_4_1.position.y = knowledgeCollector.wingInitialPosition_4_1.y - MathUtils.smootherstep(knowledgeCollector.elapsedTime_2, 0, 0.6) * 2
        knowledgeCollector.wing_4_2.position.y = knowledgeCollector.wingInitialPosition_4_2.y + MathUtils.smootherstep(knowledgeCollector.elapsedTime_2, 0, 0.6) * 2
        knowledgeCollector.wing_5_1.position.y = knowledgeCollector.wingInitialPosition_5_1.y - MathUtils.smootherstep(knowledgeCollector.elapsedTime_2, 0, 0.6) * 2
        knowledgeCollector.wing_5_2.position.y = knowledgeCollector.wingInitialPosition_5_2.y + MathUtils.smootherstep(knowledgeCollector.elapsedTime_2, 0, 0.6) * 2
        knowledgeCollector.wing_6_1.position.y = knowledgeCollector.wingInitialPosition_6_1.y - MathUtils.smootherstep(knowledgeCollector.elapsedTime_2, 0, 0.6) * 2
        knowledgeCollector.wing_6_2.position.y = knowledgeCollector.wingInitialPosition_6_2.y + MathUtils.smootherstep(knowledgeCollector.elapsedTime_2, 0, 0.6) * 2

        knowledgeCollector.projector_1.position.y = knowledgeCollector.projectorIntialPosition_1.y - MathUtils.smootherstep(knowledgeCollector.elapsedTime_2, 0, 0.6) * 2.25
        knowledgeCollector.projector_2.position.y = knowledgeCollector.projectorIntialPosition_2.y + MathUtils.smootherstep(knowledgeCollector.elapsedTime_2, 0, 0.6) * 2.25
    }
    
    if(knowledgeCollector.elapsedTime_1 > 0.6 && knowledgeCollector.elapsedTime_1 < 1.1)
    {
        knowledgeCollector.elapsedTime_3 = knowledgeCollector.elapsedTime_1 - 0.6

        knowledgeCollector.group_1.rotation.x = knowledgeCollector.groupInitialRotation_1.x + MathUtils.smoothstep(knowledgeCollector.elapsedTime_3, 0, 0.5) * Math.PI * 1.05
        knowledgeCollector.group_2.rotation.z = knowledgeCollector.groupInitialRotation_2.z - MathUtils.smoothstep(knowledgeCollector.elapsedTime_3, 0, 0.5) * 0.5 * Math.PI 
    }

    if(knowledgeCollector.elapsedTime_1 > 0.5 && knowledgeCollector.elapsedTime_1 < 1.9)
    {
        knowledgeCollector.elapsedTime_4 = knowledgeCollector.elapsedTime_1 - 0.5

        knowledgeCollector.group_1.rotation.y = knowledgeCollector.groupInitialRotation_1.y - MathUtils.smoothstep(knowledgeCollector.elapsedTime_4, 0, 1.4) * 3 * Math.PI
    }

    if(knowledgeCollector.elapsedTime_1 > 0.9 && knowledgeCollector.elapsedTime_1 < 1.9)
    {
        knowledgeCollector.elapsedTime_5 = knowledgeCollector.elapsedTime_1 - 0.9

        knowledgeCollector.group_5.position.x = knowledgeCollector.groupInitialPosition_5.x - MathUtils.smoothstep(knowledgeCollector.elapsedTime_5, 0, 1) * knowledgeCollector.groupInitialPosition_5.x
        knowledgeCollector.group_5.position.y = knowledgeCollector.groupInitialPosition_5.y - MathUtils.smoothstep(knowledgeCollector.elapsedTime_5, 0, 1) * 0.8
        knowledgeCollector.group_5.position.z = knowledgeCollector.groupInitialPosition_5.z + MathUtils.smoothstep(knowledgeCollector.elapsedTime_5, 0, 1) * 4
    }

    if(knowledgeCollector.elapsedTime_1 > 2)
    {
        firstPersonMovementObj.freeze = false
        firstPersonLookAroundObj.freeze = false
        knowledgeCollector.animationProtectionShield = false
        knowledgeCollector.cancelAnimationFrame = true
        
        knowledgeCollector.clock.stop()
        resetKnowledgeCollectorSegmentsLocation()
    }

    knowledgeCollector.animationFrame = requestAnimationFrame(hoverBackAnimationLoop)
    if(knowledgeCollector.cancelAnimationFrame) window.cancelAnimationFrame(knowledgeCollector.animationFrame)
}









export
{
    hoverBackAnimationLoop
}