import {knowledgeCollector} from "../knowledgeCollector/knowledgeCollector.js"
import {resourceInterface} from "./resourceInterface"









const initializeDeployment = () =>
{
    knowledgeCollector.groupInitialPosition_6 = knowledgeCollector.group_6.position.clone()
    resourceInterface.particlesInitialPosition = resourceInterface.particles.position.clone()

    resourceInterface.initializeDeployment = true
    resourceInterface.particles.visible = true
    resourceInterface.beam.visible = true
    
    resourceInterface.clock.start()
}









export
{
    initializeDeployment
}