import {resourceInterface} from "./resourceInterface.js"
import {scene} from "../../script.js"








const disposeResourceInterfaceSubSystems = () =>
{
    resourceInterface.beam.geometry.dispose()
    resourceInterface.beam.material.dispose()
    resourceInterface.particles.geometry.dispose()
    resourceInterface.particles.material.dispose()
    
    scene.remove(resourceInterface.beam, resourceInterface.particles)
}









export
{
    disposeResourceInterfaceSubSystems
}