import {knowledgeCollector} from "./knowledgeCollector.js"









const initializeHoverBack = () =>
{
    knowledgeCollector.projectorIntialPosition_1 = knowledgeCollector.projector_1.position.clone()
    knowledgeCollector.projectorIntialPosition_2 = knowledgeCollector.projector_2.position.clone()
    knowledgeCollector.wingInitialPosition_1_1 = knowledgeCollector.wing_1_1.position.clone()
    knowledgeCollector.wingInitialPosition_1_2 = knowledgeCollector.wing_1_2.position.clone()
    knowledgeCollector.wingInitialPosition_2_1 = knowledgeCollector.wing_2_1.position.clone()
    knowledgeCollector.wingInitialPosition_2_2 = knowledgeCollector.wing_2_2.position.clone()
    knowledgeCollector.wingInitialPosition_3_1 = knowledgeCollector.wing_3_1.position.clone()
    knowledgeCollector.wingInitialPosition_3_2 = knowledgeCollector.wing_3_2.position.clone()
    knowledgeCollector.wingInitialPosition_4_1 = knowledgeCollector.wing_4_1.position.clone()
    knowledgeCollector.wingInitialPosition_4_2 = knowledgeCollector.wing_4_2.position.clone()
    knowledgeCollector.wingInitialPosition_5_1 = knowledgeCollector.wing_5_1.position.clone()
    knowledgeCollector.wingInitialPosition_5_2 = knowledgeCollector.wing_5_2.position.clone()
    knowledgeCollector.wingInitialPosition_6_1 = knowledgeCollector.wing_6_1.position.clone()
    knowledgeCollector.wingInitialPosition_6_2 = knowledgeCollector.wing_6_2.position.clone()
    knowledgeCollector.groupInitialPosition_5 = knowledgeCollector.group_5.position.clone()
    knowledgeCollector.groupInitialRotation_1 = knowledgeCollector.group_1.rotation.clone()
    knowledgeCollector.groupInitialRotation_2 = knowledgeCollector.group_2.rotation.clone()
    knowledgeCollector.groupInitialRotation_3 = knowledgeCollector.group_3.rotation.clone()
    knowledgeCollector.groupInitialRotation_4 = knowledgeCollector.group_4.rotation.clone()
    knowledgeCollector.groupInitialRotation_5 = knowledgeCollector.group_5.rotation.clone()

    knowledgeCollector.previousSmootherstep = 0
    knowledgeCollector.currentSmootherstep = 0
    knowledgeCollector.deltaSmootherstep = 0

    knowledgeCollector.animationProtectionShield = true
    knowledgeCollector.cancelAnimationFrame = false
    knowledgeCollector.clock.start()
}









export
{
    initializeHoverBack
}