import {deactivateKnowledgeCollectorUIAnimationLoop} from "../knowledgeCollectorUI/deactivateKnowledgeCollectorUIAnimationLoop.js"
import {activateKnowledgeCollectorUIAnimationLoop} from "../knowledgeCollectorUI/activateKnowledgeCollectorUIAnimationLoop.js"
import {initializeDeactivateKnowledgeCollectorUI} from "../knowledgeCollectorUI/initializeDeactivateKnowledgeCollectorUI.js"
import {visibilityKnowledgeCollectorUIRingSystem} from "../knowledgeCollectorUI/visibilityKnowledgeCollectorUIRingSystem.js"
import {resetKnowledgeCollectorSegmentsLocation} from "../knowledgeCollector/resetKnowledgeCollectorSegmentsLocation.js"
import {initializeActivateKnowledgeCollectorUI} from "../knowledgeCollectorUI/initializeActivateKnowledgeCollectorUI.js"
import {vonAussenHerbeibefuerteFirstPersonTranslationVerzoegerung} from "../firstPerson/firstPersonFunctionality.js"
import {resetResourceInterfaceGateSegmentsLocation} from "./resetResourceInterfaceGateSegmentsLocation.js"
import {resetKnowledgeCollectorUI} from "../knowledgeCollectorUI/resetKnowledgeCollectorUI.js"
import {disposeResourceInterfaceSubSystems} from "./disposeResourceInterfaceSubSystems.js"
import {addKnowledgeCollectorToFirstperson} from "./addKnowledgeCollectorToFirstPerson.js"
import {deletePreviousNode} from "../knowledgeCollectorUI/deletePreviousNode.js"
import {setPreviousNode} from "../knowledgeCollectorUI/setPreviousNode.js"
import {initializeTransferStage_1} from "./initializeTransferStage_1.js"
import {initializeTransferStage_2} from "./initializeTransferStage_2.js"
import {coordinateTransformation} from "./coordinateTransformation.js"
import {deploymentAnimationLoop} from "./deploymentAnimationLoop.js"
import {transferAnimationLoop} from "./transferAnimationLoop.js"
import {initializeDeployment} from "./initializeDeployment.js"
import {resourceInterface} from "./resourceInterface.js"







 

const resourceInterfaceEvent = window.addEventListener("keydown", (Event) =>
{
    if(Event.key === "m" && !resourceInterface.initializeDeployment)
    {
        initializeDeployment()
        deploymentAnimationLoop()
        setTimeout(() =>
        {
            initializeActivateKnowledgeCollectorUI(false, "211", "contentCarrier_3", [1.3, 0.9, 1], "group_3", [0, 0, -1.4])
            activateKnowledgeCollectorUIAnimationLoop()
        }, 10700)
        setTimeout(() =>
        {
            resetResourceInterfaceGateSegmentsLocation()
            disposeResourceInterfaceSubSystems()
        }, 16400)
    }
    else if(Event.key === "Enter" && resourceInterface.deployment && !resourceInterface.initializeTransfer)
    {
        initializeTransferStage_1()
        vonAussenHerbeibefuerteFirstPersonTranslationVerzoegerung()
        setTimeout(() =>
        {
            initializeDeactivateKnowledgeCollectorUI()
            deactivateKnowledgeCollectorUIAnimationLoop()
        }, 400)
        setTimeout(() =>
        {
            setPreviousNode()
            deletePreviousNode()
            resetKnowledgeCollectorUI()
            visibilityKnowledgeCollectorUIRingSystem()
            coordinateTransformation() 
            addKnowledgeCollectorToFirstperson()
            initializeTransferStage_2()
            transferAnimationLoop()
        }, 1100)
        setTimeout(() =>
        {
            resetKnowledgeCollectorSegmentsLocation()
        },3600)
    }
})









export
{
    resourceInterfaceEvent
}