import { MathUtils, Mesh } from "three"
import { gateObj } from "./gateObject"
import { scene } from "../../script"







//Funktionsaufruf beim laden des skriptes, ggf
const setBiometricLaserScann = () =>
{
    gateObj.laserMeshArray.push(new Mesh(gateObj.laserGeometry, gateObj.laserMaterial_0))
    gateObj.laserMeshArray.push(new Mesh(gateObj.laserGeometry, gateObj.laserMaterial_1))

    scene.add(...gateObj.laserMeshArray)

    biometricScannLoop()
}









const biometricScannLoop = () =>
{
    gateObj.elapsedTime_0 = gateObj.time_0.getElapsedTime()

    if(gateObj.elapsedTime_0 < 1.6)
    {
        gateObj.laserMeshArray[0].material.uniforms.uTime.value = gateObj.elapsedTime_0
        gateObj.laserMeshArray[1].material.uniforms.uTime.value = gateObj.elapsedTime_0
    }
    else
    {
        gateObj.time_0.stop()
        gateObj.cancelAnimationFrame_0 = true

        cleanBiometricScann()
        gateMechanicOpening()
    }

    gateObj.animationFrame_0 = requestAnimationFrame(biometricScannLoop)
    if(gateObj.cancelAnimationFrame_0) window.cancelAnimationFrame(gateObj.animationFrame_0)
}   









const cleanBiometricScann = () =>
{
    scene.remove(gateObj.laserMeshArray[0])
    gateObj.laserMeshArray[0].geometry.dispose()
    gateObj.laserMeshArray[0].material.dispose()

    scene.remove(gateObj.laserMeshArray[1])
    gateObj.laserMeshArray[1].geometry.dispose()
    gateObj.laserMeshArray[1].material.dispose()
}




const gateMechanicOpening = () =>
{
    gateObj.elapsedTime_1 = gateObj.time_1.getElapsedTime()

    if(!gateObj.open && gateObj.elapsedTime_1 <= 2 )
    {
        gateObj.gateLeft.position.x = gateObj.gateLeftStartPos.x - (MathUtils.smootherstep(gateObj.elapsedTime_1, 0, 2) * 5.1)
        gateObj.gateLeftEndPos = gateObj.gateLeft.position.clone()

        gateObj.gateRight.position.x = gateObj.gateRightStartPos.x + (MathUtils.smootherstep(gateObj.elapsedTime_1, 0, 2) * 5.1)
        gateObj.gateRightEndPos = gateObj.gateRight.position.clone()
        
        gateObj.gateTop.position.y = gateObj.gateTopStartPos.y + (MathUtils.smootherstep(gateObj.elapsedTime_1, 0, 2) * 2.8)
        gateObj.gateTopEndPos = gateObj.gateTop.position.clone()

        gateObj.gateBottom.position.y = gateObj.gateBottomStartPos.y - (MathUtils.smootherstep(gateObj.elapsedTime_1, 0, 2) * 2.8)
        gateObj.gateBottomEndPos = gateObj.gateBottom.position.clone()
    }
    else
    {
        gateObj.open = true
        gateObj.cancelAnimationFrame_1 = true
        gateObj.time_1.stop()
    }

    gateObj.animationFrame_1 = requestAnimationFrame(gateMechanicOpening)
    if(gateObj.cancelAnimationFrame_1) window.cancelAnimationFrame(gateObj.animationFrame_1)
}







const gateMechanicClosing = () =>
{
    gateObj.elapsedTime_2 = gateObj.time_2.getElapsedTime()

    if(gateObj.elapsedTime_2 <= 2)
    {
        gateObj.gateLeft.position.x = gateObj.gateLeftEndPos.x + (MathUtils.smootherstep(gateObj.elapsedTime_2, 0, 2) * 5.1)
        gateObj.gateRight.position.x = gateObj.gateRightEndPos.x - (MathUtils.smootherstep(gateObj.elapsedTime_2, 0, 2) * 5.1)
        gateObj.gateTop.position.y = gateObj.gateTopEndPos.y - (MathUtils.smootherstep(gateObj.elapsedTime_2, 0, 2) * 2.8)
        gateObj.gateBottom.position.y = gateObj.gateBottomEndPos.y + (MathUtils.smootherstep(gateObj.elapsedTime_2, 0, 2) * 2.8)
    }
    else
    {
        gateObj.gateLeft.position.x = gateObj.gateLeftStartPos.x//exacte posi
        gateObj.gateRight.position.x = gateObj.gateRightStartPos.x
        gateObj.gateTop.position.y = gateObj.gateTopStartPos.y
        gateObj.gateBottom.position.y = gateObj.gateBottomStartPos.y
        
        gateObj.cancelAnimationFrame_1 = true
        gateObj.time_2.stop()
    }

    gateObj.animationFrame_1 = requestAnimationFrame(gateMechanicClosing)
    if(gateObj.cancelAnimationFrame_1) window.cancelAnimationFrame(gateObj.animationFrame_1)

}









export
{
    setBiometricLaserScann,
    gateMechanicOpening,
    gateMechanicClosing
}









