import {knowledgeCollector} from "../knowledgeCollector/knowledgeCollector.js"
import {resourceInterface} from "./resourceInterface.js"
import {firstPersonCam} from "../../script.js"
import {MathUtils} from "three"









const deploymentAnimationLoop = () =>
{
    resourceInterface.elapsedTime_1 = resourceInterface.clock.getElapsedTime()

    if(resourceInterface.elapsedTime_1 < 1)
    {
        resourceInterface.segment_1.position.y = resourceInterface.segmentIntialPosition_1.y - (MathUtils.smoothstep(resourceInterface.elapsedTime_1, 0, 1) * 0.09)
        resourceInterface.segment_2.position.y = resourceInterface.segmentIntialPosition_2.y - (MathUtils.smoothstep(resourceInterface.elapsedTime_1, 0, 1) * 0.09)
        resourceInterface.segment_3.position.y = resourceInterface.segmentIntialPosition_3.y - (MathUtils.smoothstep(resourceInterface.elapsedTime_1, 0, 1) * 0.09)
        resourceInterface.segment_4.position.y = resourceInterface.segmentIntialPosition_4.y - (MathUtils.smoothstep(resourceInterface.elapsedTime_1, 0, 1) * 0.09)
        resourceInterface.segment_5.position.y = resourceInterface.segmentIntialPosition_5.y - (MathUtils.smoothstep(resourceInterface.elapsedTime_1, 0, 1) * 0.09)
        resourceInterface.segment_6.position.y = resourceInterface.segmentIntialPosition_6.y - (MathUtils.smoothstep(resourceInterface.elapsedTime_1, 0, 1) * 0.09)
        resourceInterface.segment_7.position.y = resourceInterface.segmentIntialPosition_7.y - (MathUtils.smoothstep(resourceInterface.elapsedTime_1, 0, 1) * 0.09)
        resourceInterface.segment_8.position.y = resourceInterface.segmentIntialPosition_8.y - (MathUtils.smoothstep(resourceInterface.elapsedTime_1, 0, 1) * 0.09)
    }
    else if(resourceInterface.elapsedTime_1 > 1 && resourceInterface.elapsedTime_1 < 7)
    {
        resourceInterface.elapsedTime_2 = resourceInterface.elapsedTime_1 - 1
        
        resourceInterface.previousSmootherstep_1 = resourceInterface.currentSmootherstep_1
        resourceInterface.currentSmootherstep_1 = MathUtils.smootherstep(resourceInterface.elapsedTime_2, 0, 6)
        resourceInterface.deltaSmootherstep_1 = resourceInterface.currentSmootherstep_1 - resourceInterface.previousSmootherstep_1

        resourceInterface.segment_1.translateZ(-resourceInterface.deltaSmootherstep_1)
        resourceInterface.segment_2.translateZ(-resourceInterface.deltaSmootherstep_1)
        resourceInterface.segment_3.translateZ(-resourceInterface.deltaSmootherstep_1)
        resourceInterface.segment_4.translateZ(-resourceInterface.deltaSmootherstep_1)
        resourceInterface.segment_5.translateZ(-resourceInterface.deltaSmootherstep_1)
        resourceInterface.segment_6.translateZ(-resourceInterface.deltaSmootherstep_1)
        resourceInterface.segment_7.translateZ(-resourceInterface.deltaSmootherstep_1)
        resourceInterface.segment_8.translateZ(-resourceInterface.deltaSmootherstep_1)

        resourceInterface.beam.scale.x += resourceInterface.deltaSmootherstep_1 * 0.35
        resourceInterface.beam.scale.z +=  0.0015

        resourceInterface.segmentIntialPosition_1 = resourceInterface.segment_1.position.clone()
        resourceInterface.segmentIntialPosition_2 = resourceInterface.segment_2.position.clone() 
        resourceInterface.segmentIntialPosition_3 = resourceInterface.segment_3.position.clone() 
        resourceInterface.segmentIntialPosition_4 = resourceInterface.segment_4.position.clone() 
        resourceInterface.segmentIntialPosition_5 = resourceInterface.segment_5.position.clone() 
        resourceInterface.segmentIntialPosition_6 = resourceInterface.segment_6.position.clone() 
        resourceInterface.segmentIntialPosition_7 = resourceInterface.segment_7.position.clone() 
        resourceInterface.segmentIntialPosition_8 = resourceInterface.segment_8.position.clone()
    }

    if(resourceInterface.elapsedTime_1 > 1 && resourceInterface.elapsedTime_1 < 9.3)
    {
        firstPersonCam.getWorldPosition(resourceInterface.lookAtTarget)
        resourceInterface.beam.lookAt(resourceInterface.lookAtTarget.x, 1.2, resourceInterface.lookAtTarget.z)
    }
    
    if(resourceInterface.elapsedTime_1 > 4 && resourceInterface.elapsedTime_1 < 8)
    {
        resourceInterface.elapsedTime_3 = resourceInterface.elapsedTime_1 - 4

        knowledgeCollector.group_6.position.y  = knowledgeCollector.groupInitialPosition_6.y  + MathUtils.smoothstep(resourceInterface.elapsedTime_3, 0, 4) * 4
        resourceInterface.particles.position.y = resourceInterface.particlesInitialPosition.y + MathUtils.smoothstep(resourceInterface.elapsedTime_3, 0, 4) * 4
    }
    else if(resourceInterface.elapsedTime_1 > 8.3 && resourceInterface.elapsedTime_1 < 9.3)
    {
        resourceInterface.elapsedTime_4 = resourceInterface.elapsedTime_1 - 8.3

        for(let i = 0 ; i < 300 ; i++)
        {
            const i3 = i * 3
            resourceInterface.particles.geometry.attributes.position.array[i3 + 1] -= resourceInterface.particles.geometry.attributes.velocity.array[i] * 1.1 * Math.pow(resourceInterface.elapsedTime_4, 2)
        }
        resourceInterface.particles.geometry.attributes.position.needsUpdate = true
        
        resourceInterface.beam.material.opacity = 0.8 - resourceInterface.elapsedTime_4 * 2
    }
    else if(resourceInterface.elapsedTime_1 > 9.3 && resourceInterface.elapsedTime_1 < 10.3)
    {
        resourceInterface.elapsedTime_5 = resourceInterface.elapsedTime_1 - 9.3
        
        knowledgeCollector.group_1.rotation.x = knowledgeCollector.groupInitialRotation_1.x + MathUtils.smoothstep(resourceInterface.elapsedTime_5, 0, 1) * Math.PI * 0.5
        knowledgeCollector.group_2.rotation.y = knowledgeCollector.groupInitialRotation_2.y + MathUtils.smoothstep(resourceInterface.elapsedTime_5, 0, 1) * Math.PI * 1.5
    }

    if(resourceInterface.elapsedTime_1 > 9.3 && resourceInterface.elapsedTime_1 < 15.3)
    {
        resourceInterface.elapsedTime_5 = resourceInterface.elapsedTime_1 - 9.3

        resourceInterface.previousSmootherstep_2 = resourceInterface.currentSmootherstep_2
        resourceInterface.currentSmootherstep_2 = MathUtils.smootherstep(resourceInterface.elapsedTime_5, 0, 6)
        resourceInterface.deltaSmootherstep_2 = resourceInterface.currentSmootherstep_2 - resourceInterface.previousSmootherstep_2

        resourceInterface.segment_1.translateZ(resourceInterface.deltaSmootherstep_2)
        resourceInterface.segment_2.translateZ(resourceInterface.deltaSmootherstep_2)
        resourceInterface.segment_3.translateZ(resourceInterface.deltaSmootherstep_2)
        resourceInterface.segment_4.translateZ(resourceInterface.deltaSmootherstep_2)
        resourceInterface.segment_5.translateZ(resourceInterface.deltaSmootherstep_2)
        resourceInterface.segment_6.translateZ(resourceInterface.deltaSmootherstep_2)
        resourceInterface.segment_7.translateZ(resourceInterface.deltaSmootherstep_2)
        resourceInterface.segment_8.translateZ(resourceInterface.deltaSmootherstep_2)
    }
    
    if(resourceInterface.elapsedTime_1 > 10 && resourceInterface.elapsedTime_1 < 10.7)
    {
        resourceInterface.elapsedTime_6 = resourceInterface.elapsedTime_1 - 10

        knowledgeCollector.previousSmootherstep = knowledgeCollector.currentSmootherstep
        knowledgeCollector.currentSmootherstep = MathUtils.smootherstep(resourceInterface.elapsedTime_6, 0, 0.7)
        knowledgeCollector.deltaSmootherstep = (knowledgeCollector.currentSmootherstep - knowledgeCollector.previousSmootherstep) * 1

        knowledgeCollector.wing_1_1.translateZ(knowledgeCollector.deltaSmootherstep)
        knowledgeCollector.wing_1_2.translateZ(knowledgeCollector.deltaSmootherstep)
        knowledgeCollector.wing_2_1.translateZ(knowledgeCollector.deltaSmootherstep)
        knowledgeCollector.wing_2_2.translateZ(knowledgeCollector.deltaSmootherstep)
        knowledgeCollector.wing_3_1.translateZ(knowledgeCollector.deltaSmootherstep)
        knowledgeCollector.wing_3_2.translateZ(knowledgeCollector.deltaSmootherstep)
        knowledgeCollector.wing_4_1.translateZ(knowledgeCollector.deltaSmootherstep)
        knowledgeCollector.wing_4_2.translateZ(knowledgeCollector.deltaSmootherstep)
        knowledgeCollector.wing_5_1.translateZ(knowledgeCollector.deltaSmootherstep)
        knowledgeCollector.wing_5_2.translateZ(knowledgeCollector.deltaSmootherstep)
        knowledgeCollector.wing_6_1.translateZ(knowledgeCollector.deltaSmootherstep)
        knowledgeCollector.wing_6_2.translateZ(knowledgeCollector.deltaSmootherstep)

        knowledgeCollector.wing_1_1.position.y = knowledgeCollector.wingInitialPosition_1_1.y + MathUtils.smootherstep(resourceInterface.elapsedTime_6, 0, 0.7) * 2
        knowledgeCollector.wing_1_2.position.y = knowledgeCollector.wingInitialPosition_1_2.y - MathUtils.smootherstep(resourceInterface.elapsedTime_6, 0, 0.7) * 2
        knowledgeCollector.wing_2_1.position.y = knowledgeCollector.wingInitialPosition_2_1.y + MathUtils.smootherstep(resourceInterface.elapsedTime_6, 0, 0.7) * 2
        knowledgeCollector.wing_2_2.position.y = knowledgeCollector.wingInitialPosition_2_2.y - MathUtils.smootherstep(resourceInterface.elapsedTime_6, 0, 0.7) * 2
        knowledgeCollector.wing_3_1.position.y = knowledgeCollector.wingInitialPosition_3_1.y + MathUtils.smootherstep(resourceInterface.elapsedTime_6, 0, 0.7) * 2
        knowledgeCollector.wing_3_2.position.y = knowledgeCollector.wingInitialPosition_3_2.y - MathUtils.smootherstep(resourceInterface.elapsedTime_6, 0, 0.7) * 2
        knowledgeCollector.wing_4_1.position.y = knowledgeCollector.wingInitialPosition_4_1.y + MathUtils.smootherstep(resourceInterface.elapsedTime_6, 0, 0.7) * 2
        knowledgeCollector.wing_4_2.position.y = knowledgeCollector.wingInitialPosition_4_2.y - MathUtils.smootherstep(resourceInterface.elapsedTime_6, 0, 0.7) * 2
        knowledgeCollector.wing_5_1.position.y = knowledgeCollector.wingInitialPosition_5_1.y + MathUtils.smootherstep(resourceInterface.elapsedTime_6, 0, 0.7) * 2
        knowledgeCollector.wing_5_2.position.y = knowledgeCollector.wingInitialPosition_5_2.y - MathUtils.smootherstep(resourceInterface.elapsedTime_6, 0, 0.7) * 2
        knowledgeCollector.wing_6_1.position.y = knowledgeCollector.wingInitialPosition_6_1.y + MathUtils.smootherstep(resourceInterface.elapsedTime_6, 0, 0.7) * 2
        knowledgeCollector.wing_6_2.position.y = knowledgeCollector.wingInitialPosition_6_2.y - MathUtils.smootherstep(resourceInterface.elapsedTime_6, 0, 0.7) * 2

        knowledgeCollector.projector_1.position.y = knowledgeCollector.projectorIntialPosition_1.y + MathUtils.smootherstep(resourceInterface.elapsedTime_6, 0, 0.7) * 2
        knowledgeCollector.projector_2.position.y = knowledgeCollector.projectorIntialPosition_2.y - MathUtils.smootherstep(resourceInterface.elapsedTime_6, 0, 0.7) * 2
    }

    if(resourceInterface.elapsedTime_1 > 15.3 && resourceInterface.elapsedTime_1 < 16.3)
    {
        resourceInterface.elapsedTime_7 = resourceInterface.elapsedTime_1 - 15.3

        resourceInterface.segment_1.position.y = resourceInterface.segmentIntialPosition_1.y + (MathUtils.smoothstep(resourceInterface.elapsedTime_7, 0, 1) * 0.09)
        resourceInterface.segment_2.position.y = resourceInterface.segmentIntialPosition_2.y + (MathUtils.smoothstep(resourceInterface.elapsedTime_7, 0, 1) * 0.09)
        resourceInterface.segment_3.position.y = resourceInterface.segmentIntialPosition_3.y + (MathUtils.smoothstep(resourceInterface.elapsedTime_7, 0, 1) * 0.09)
        resourceInterface.segment_4.position.y = resourceInterface.segmentIntialPosition_4.y + (MathUtils.smoothstep(resourceInterface.elapsedTime_7, 0, 1) * 0.09)
        resourceInterface.segment_5.position.y = resourceInterface.segmentIntialPosition_5.y + (MathUtils.smoothstep(resourceInterface.elapsedTime_7, 0, 1) * 0.09)
        resourceInterface.segment_6.position.y = resourceInterface.segmentIntialPosition_6.y + (MathUtils.smoothstep(resourceInterface.elapsedTime_7, 0, 1) * 0.09)
        resourceInterface.segment_7.position.y = resourceInterface.segmentIntialPosition_7.y + (MathUtils.smoothstep(resourceInterface.elapsedTime_7, 0, 1) * 0.09)
        resourceInterface.segment_8.position.y = resourceInterface.segmentIntialPosition_8.y + (MathUtils.smoothstep(resourceInterface.elapsedTime_7, 0, 1) * 0.09)
    }
    else if(resourceInterface.elapsedTime_1 > 16.3)
    {
        resourceInterface.deployment = true
        resourceInterface.cancelAnimationFrame = true
        resourceInterface.clock.stop()
    }

    resourceInterface.animationFrame = requestAnimationFrame(deploymentAnimationLoop)
    if(resourceInterface.cancelAnimationFrame) window.cancelAnimationFrame(resourceInterface.animationFrame)
}









export
{
    deploymentAnimationLoop
}



















