import {teleporter} from "./teleporter.js"
import {scene} from "../../script.js"









const generateTeleporterGroupHierarchy = () =>
{
    teleporter.group_1.name = "group_1"
    teleporter.group_2.name = "group_2"
    teleporter.group_3.name = "group_3"
    teleporter.group_4.name = "group_4"

    teleporter.group_1.add(teleporter.ring_1)
    teleporter.group_2.add(teleporter.ring_2)
    teleporter.group_3.add(teleporter.ring_3)
    teleporter.group_4.add(teleporter.ring_4)

    teleporter.group_1.add(teleporter.group_2)
    teleporter.group_2.add(teleporter.group_3)
    teleporter.group_3.add(teleporter.group_4)

    teleporter.group_1.position.set(0, 2, 94,69)
    
    scene.add(teleporter.group_1)
}









export
{
    generateTeleporterGroupHierarchy
}