import {deactivateKnowledgeCollectorUIAnimationLoop} from "../knowledgeCollectorUI/deactivateKnowledgeCollectorUIAnimationLoop.js"
import {visibilityKnowledgeCollectorUIRingSystem} from "../knowledgeCollectorUI/visibilityKnowledgeCollectorUIRingSystem.js"
import {initializeDeactivateKnowledgeCollectorUI} from "../knowledgeCollectorUI/initializeDeactivateKnowledgeCollectorUI.js"
import {vonAussenHerbeibefuerteFirstPersonTranslationVerzoegerung} from "../firstPerson/firstPersonFunctionality.js"
import {setGroupHierarchyLocationForLituusSpiral} from "./setGroupHierarchyLocationForLituusSpiral.js"
import {resetKnowledgeCollectorUI} from "../knowledgeCollectorUI/resetKnowledgeCollectorUI.js"
import {knowledgeCollectorUI} from "../knowledgeCollectorUI/knowledgeCollectorUI.js"
import {deletePreviousNode} from "../knowledgeCollectorUI/deletePreviousNode.js"
import {resourceInterface} from "../resourceInterface/resourceInterface.js"
import {setPreviousNode} from "../knowledgeCollectorUI/setPreviousNode.js"
import {initializeHoverOutStage_1} from "./initializeHoverOutStage_1.js"
import {initializeHoverOutStage_2} from "./initializeHoverOutStage_2.js"
import {hoverBackAnimationLoop} from "./hoverBackAnimationLoop.js"
import {hoverOutAnimationLoop} from "./hoverOutAnimationLoop.js"
import {initializeHoverBack} from "./initializeHoverBack.js"
import {knowledgeCollector} from "./knowledgeCollector.js"









window.addEventListener("keydown", (Event) =>
{
    if(Event.key === "b" && resourceInterface.transferSuccessful)
    {
        if(!knowledgeCollector.animationProtectionShield && !knowledgeCollectorUI.enabled)
        {
            initializeHoverOutStage_1()
            vonAussenHerbeibefuerteFirstPersonTranslationVerzoegerung()
            setTimeout(() =>
            {
                setGroupHierarchyLocationForLituusSpiral()
                initializeHoverOutStage_2()
                hoverOutAnimationLoop()
            }, 200)
        }
        else if(!knowledgeCollector.animationProtectionShield && knowledgeCollectorUI.enabled && !knowledgeCollector.animationProtectionShield)
        {
            initializeDeactivateKnowledgeCollectorUI()
            deactivateKnowledgeCollectorUIAnimationLoop()
            setTimeout(() =>
            {
                setPreviousNode()
                deletePreviousNode()
                resetKnowledgeCollectorUI()
                visibilityKnowledgeCollectorUIRingSystem()
                initializeHoverBack()
                hoverBackAnimationLoop()
            }, 700)
        }
    }
})









