import {knowledgeCollectorUI} from "./knowledgeCollectorUI.js"
import {MathUtils} from "three"
import { knowledgeCollector } from "../knowledgeCollector/knowledgeCollector.js"









const deactivateKnowledgeCollectorUIAnimationLoop = () =>
{
    knowledgeCollectorUI.elapsedTimeDeactivateKnowledgeCollectorUI_1 = knowledgeCollectorUI.clockDeactivateKnowledgeCollectorUI.getElapsedTime()

    if(knowledgeCollectorUI.elapsedTimeDeactivateKnowledgeCollectorUI_1 < 0.1)
    {
        knowledgeCollectorUI.variableOpacityStep_1 = MathUtils.smoothstep(knowledgeCollectorUI.elapsedTimeDeactivateKnowledgeCollectorUI_1, 0, 0.1) * 0.8

        knowledgeCollectorUI[knowledgeCollectorUI.currentContentCarrier].material.opacity = knowledgeCollectorUI.initialOpacity_1 - knowledgeCollectorUI.variableOpacityStep_1
        knowledgeCollectorUI.graph[knowledgeCollectorUI.graph.currentNode].mesh.material.opacity = knowledgeCollectorUI.initialOpacity_1 - knowledgeCollectorUI.variableOpacityStep_1
    }
    else if(!knowledgeCollectorUI.oneTimeIteration)
    {
        knowledgeCollectorUI[knowledgeCollectorUI.currentContentCarrier].material.opacity = 0
        knowledgeCollectorUI.graph[knowledgeCollectorUI.graph.currentNode].mesh.material.opacity = 0

        knowledgeCollectorUI.oneTimeIteration = true
    }

    if(knowledgeCollectorUI.elapsedTimeDeactivateKnowledgeCollectorUI_1 > 0.05 && knowledgeCollectorUI.elapsedTimeDeactivateKnowledgeCollectorUI_1 < 0.15)
    {
        knowledgeCollectorUI.elapsedTimeDeactivateKnowledgeCollectorUI_2 = knowledgeCollectorUI.elapsedTimeDeactivateKnowledgeCollectorUI_1 - 0.05

        knowledgeCollectorUI.variablePositionStep_1 = MathUtils.smoothstep(knowledgeCollectorUI.elapsedTimeDeactivateKnowledgeCollectorUI_2, 0, 0.1) * (knowledgeCollectorUI.ringLeftResetPosition_1.y - knowledgeCollectorUI.ringLeftInitialPosition_1.y)
        knowledgeCollectorUI.variablePositionStep_2 = MathUtils.smoothstep(knowledgeCollectorUI.elapsedTimeDeactivateKnowledgeCollectorUI_2, 0, 0.1) * (knowledgeCollectorUI.ringLeftResetPosition_2.y - knowledgeCollectorUI.ringLeftInitialPosition_2.y)
        knowledgeCollectorUI.variablePositionStep_3 = MathUtils.smoothstep(knowledgeCollectorUI.elapsedTimeDeactivateKnowledgeCollectorUI_2, 0, 0.1) * (knowledgeCollectorUI.ringLeftResetPosition_3.y - knowledgeCollectorUI.ringLeftInitialPosition_3.y)
        knowledgeCollectorUI.variablePositionStep_4 = MathUtils.smoothstep(knowledgeCollectorUI.elapsedTimeDeactivateKnowledgeCollectorUI_2, 0, 0.1) * (knowledgeCollectorUI.ringLeftResetPosition_4.y - knowledgeCollectorUI.ringLeftInitialPosition_4.y)
        knowledgeCollectorUI.variablePositionStep_5 = MathUtils.smoothstep(knowledgeCollectorUI.elapsedTimeDeactivateKnowledgeCollectorUI_2, 0, 0.1) * (knowledgeCollectorUI.ringLeftResetPosition_5.y - knowledgeCollectorUI.ringLeftInitialPosition_5.y)
        knowledgeCollectorUI.variablePositionStep_6 = MathUtils.smoothstep(knowledgeCollectorUI.elapsedTimeDeactivateKnowledgeCollectorUI_2, 0, 0.1) * (knowledgeCollectorUI.ringLeftResetPosition_6.y - knowledgeCollectorUI.ringLeftInitialPosition_6.y)

        knowledgeCollectorUI.ringLeft_1.position.y = knowledgeCollectorUI.ringLeftInitialPosition_1.y + knowledgeCollectorUI.variablePositionStep_1
        knowledgeCollectorUI.ringLeft_2.position.y = knowledgeCollectorUI.ringLeftInitialPosition_2.y + knowledgeCollectorUI.variablePositionStep_2
        knowledgeCollectorUI.ringLeft_3.position.y = knowledgeCollectorUI.ringLeftInitialPosition_3.y + knowledgeCollectorUI.variablePositionStep_3
        knowledgeCollectorUI.ringLeft_4.position.y = knowledgeCollectorUI.ringLeftInitialPosition_4.y + knowledgeCollectorUI.variablePositionStep_4
        knowledgeCollectorUI.ringLeft_5.position.y = knowledgeCollectorUI.ringLeftInitialPosition_5.y + knowledgeCollectorUI.variablePositionStep_5
        knowledgeCollectorUI.ringLeft_6.position.y = knowledgeCollectorUI.ringLeftInitialPosition_6.y + knowledgeCollectorUI.variablePositionStep_6
        
        knowledgeCollectorUI.ringRight_1.position.y = knowledgeCollectorUI.ringRightInitialPosition_1.y - knowledgeCollectorUI.variablePositionStep_1
        knowledgeCollectorUI.ringRight_2.position.y = knowledgeCollectorUI.ringRightInitialPosition_2.y - knowledgeCollectorUI.variablePositionStep_2
        knowledgeCollectorUI.ringRight_3.position.y = knowledgeCollectorUI.ringRightInitialPosition_3.y - knowledgeCollectorUI.variablePositionStep_3
        knowledgeCollectorUI.ringRight_4.position.y = knowledgeCollectorUI.ringRightInitialPosition_4.y - knowledgeCollectorUI.variablePositionStep_4
        knowledgeCollectorUI.ringRight_5.position.y = knowledgeCollectorUI.ringRightInitialPosition_5.y - knowledgeCollectorUI.variablePositionStep_5
        knowledgeCollectorUI.ringRight_6.position.y = knowledgeCollectorUI.ringRightInitialPosition_6.y - knowledgeCollectorUI.variablePositionStep_6
    }

    if(knowledgeCollectorUI.elapsedTimeDeactivateKnowledgeCollectorUI_1 > 0.25 && knowledgeCollectorUI.elapsedTimeDeactivateKnowledgeCollectorUI_1 < 0.45)
    {
        knowledgeCollectorUI.elapsedTimeDeactivateKnowledgeCollectorUI_3 = knowledgeCollectorUI.elapsedTimeDeactivateKnowledgeCollectorUI_1 - 0.25

        knowledgeCollectorUI.variableRotationStep_1 = 0.1 * Math.PI * Math.sin(4 * Math.PI / 0.2 * knowledgeCollectorUI.elapsedTimeDeactivateKnowledgeCollectorUI_3) 

        knowledgeCollectorUI.ringLeft_1.rotation.z = knowledgeCollectorUI.ringLeftInitialRotation_1.z + knowledgeCollectorUI.variableRotationStep_1
        knowledgeCollectorUI.ringLeft_2.rotation.z = knowledgeCollectorUI.ringLeftInitialRotation_2.z - knowledgeCollectorUI.variableRotationStep_1
        knowledgeCollectorUI.ringLeft_3.rotation.z = knowledgeCollectorUI.ringLeftInitialRotation_3.z + knowledgeCollectorUI.variableRotationStep_1
        knowledgeCollectorUI.ringLeft_4.rotation.z = knowledgeCollectorUI.ringLeftInitialRotation_4.z - knowledgeCollectorUI.variableRotationStep_1
        knowledgeCollectorUI.ringLeft_5.rotation.z = knowledgeCollectorUI.ringLeftInitialRotation_5.z + knowledgeCollectorUI.variableRotationStep_1
        knowledgeCollectorUI.ringLeft_6.rotation.z = knowledgeCollectorUI.ringLeftInitialRotation_6.z - knowledgeCollectorUI.variableRotationStep_1
        
        knowledgeCollectorUI.ringRight_1.rotation.z = knowledgeCollectorUI.ringRightInitialRotation_1.z + knowledgeCollectorUI.variableRotationStep_1
        knowledgeCollectorUI.ringRight_2.rotation.z = knowledgeCollectorUI.ringRightInitialRotation_2.z - knowledgeCollectorUI.variableRotationStep_1
        knowledgeCollectorUI.ringRight_3.rotation.z = knowledgeCollectorUI.ringRightInitialRotation_3.z + knowledgeCollectorUI.variableRotationStep_1
        knowledgeCollectorUI.ringRight_4.rotation.z = knowledgeCollectorUI.ringRightInitialRotation_4.z - knowledgeCollectorUI.variableRotationStep_1
        knowledgeCollectorUI.ringRight_5.rotation.z = knowledgeCollectorUI.ringRightInitialRotation_5.z + knowledgeCollectorUI.variableRotationStep_1
        knowledgeCollectorUI.ringRight_6.rotation.z = knowledgeCollectorUI.ringRightInitialRotation_6.z - knowledgeCollectorUI.variableRotationStep_1
    }

    if(knowledgeCollectorUI.elapsedTimeDeactivateKnowledgeCollectorUI_1 > 0.35 && knowledgeCollectorUI.elapsedTimeDeactivateKnowledgeCollectorUI_1 < 0.55)
    {
        knowledgeCollectorUI.elapsedTimeDeactivateKnowledgeCollectorUI_5 = knowledgeCollectorUI.elapsedTimeDeactivateKnowledgeCollectorUI_1 - 0.35

        knowledgeCollectorUI.variableOpacityStep_2 = 0.2 * Math.cos(Math.PI * (45 / 2) * knowledgeCollectorUI.elapsedTimeDeactivateKnowledgeCollectorUI_5)

        knowledgeCollectorUI.ringLeft_1.material.opacity = knowledgeCollectorUI.initialOpacity_2 + knowledgeCollectorUI.variableOpacityStep_2
        knowledgeCollectorUI.ringLeft_2.material.opacity = knowledgeCollectorUI.initialOpacity_2 + knowledgeCollectorUI.variableOpacityStep_2
        knowledgeCollectorUI.ringLeft_3.material.opacity = knowledgeCollectorUI.initialOpacity_2 + knowledgeCollectorUI.variableOpacityStep_2
        knowledgeCollectorUI.ringLeft_4.material.opacity = knowledgeCollectorUI.initialOpacity_2 + knowledgeCollectorUI.variableOpacityStep_2
        knowledgeCollectorUI.ringLeft_5.material.opacity = knowledgeCollectorUI.initialOpacity_2 + knowledgeCollectorUI.variableOpacityStep_2
        knowledgeCollectorUI.ringLeft_6.material.opacity = knowledgeCollectorUI.initialOpacity_2 + knowledgeCollectorUI.variableOpacityStep_2
        
        knowledgeCollectorUI.ringRight_1.material.opacity = knowledgeCollectorUI.initialOpacity_2 + knowledgeCollectorUI.variableOpacityStep_2
        knowledgeCollectorUI.ringRight_2.material.opacity = knowledgeCollectorUI.initialOpacity_2 + knowledgeCollectorUI.variableOpacityStep_2
        knowledgeCollectorUI.ringRight_3.material.opacity = knowledgeCollectorUI.initialOpacity_2 + knowledgeCollectorUI.variableOpacityStep_2
        knowledgeCollectorUI.ringRight_4.material.opacity = knowledgeCollectorUI.initialOpacity_2 + knowledgeCollectorUI.variableOpacityStep_2
        knowledgeCollectorUI.ringRight_5.material.opacity = knowledgeCollectorUI.initialOpacity_2 + knowledgeCollectorUI.variableOpacityStep_2
        knowledgeCollectorUI.ringRight_6.material.opacity = knowledgeCollectorUI.initialOpacity_2 + knowledgeCollectorUI.variableOpacityStep_2
    }

    if(knowledgeCollectorUI.elapsedTimeDeactivateKnowledgeCollectorUI_1 > 0.55 && knowledgeCollectorUI.elapsedTimeDeactivateKnowledgeCollectorUI_1 < 0.65)
    {
        knowledgeCollectorUI.elapsedTimeDeactivateKnowledgeCollectorUI_6 = knowledgeCollectorUI.elapsedTimeDeactivateKnowledgeCollectorUI_1 - 0.55

        knowledgeCollectorUI.variableOpacityStep_2 = MathUtils.smoothstep(knowledgeCollectorUI.elapsedTimeDeactivateKnowledgeCollectorUI_6, 0, 0.1) * 0.8

        knowledgeCollectorUI.ringLeft_1.material.opacity = knowledgeCollectorUI.initialOpacity_1 - knowledgeCollectorUI.variableOpacityStep_2
        knowledgeCollectorUI.ringLeft_2.material.opacity = knowledgeCollectorUI.initialOpacity_1 - knowledgeCollectorUI.variableOpacityStep_2
        knowledgeCollectorUI.ringLeft_3.material.opacity = knowledgeCollectorUI.initialOpacity_1 - knowledgeCollectorUI.variableOpacityStep_2
        knowledgeCollectorUI.ringLeft_4.material.opacity = knowledgeCollectorUI.initialOpacity_1 - knowledgeCollectorUI.variableOpacityStep_2
        knowledgeCollectorUI.ringLeft_5.material.opacity = knowledgeCollectorUI.initialOpacity_1 - knowledgeCollectorUI.variableOpacityStep_2
        knowledgeCollectorUI.ringLeft_6.material.opacity = knowledgeCollectorUI.initialOpacity_1 - knowledgeCollectorUI.variableOpacityStep_2
        
        knowledgeCollectorUI.ringRight_1.material.opacity = knowledgeCollectorUI.initialOpacity_1 - knowledgeCollectorUI.variableOpacityStep_2
        knowledgeCollectorUI.ringRight_2.material.opacity = knowledgeCollectorUI.initialOpacity_1 - knowledgeCollectorUI.variableOpacityStep_2
        knowledgeCollectorUI.ringRight_3.material.opacity = knowledgeCollectorUI.initialOpacity_1 - knowledgeCollectorUI.variableOpacityStep_2
        knowledgeCollectorUI.ringRight_4.material.opacity = knowledgeCollectorUI.initialOpacity_1 - knowledgeCollectorUI.variableOpacityStep_2
        knowledgeCollectorUI.ringRight_5.material.opacity = knowledgeCollectorUI.initialOpacity_1 - knowledgeCollectorUI.variableOpacityStep_2
        knowledgeCollectorUI.ringRight_6.material.opacity = knowledgeCollectorUI.initialOpacity_1 - knowledgeCollectorUI.variableOpacityStep_2
    }

    if(knowledgeCollectorUI.elapsedTimeDeactivateKnowledgeCollectorUI_1 > 0.45 && knowledgeCollectorUI.elapsedTimeDeactivateKnowledgeCollectorUI_1 < 0.65)
    {
        knowledgeCollectorUI.elapsedTimeDeactivateKnowledgeCollectorUI_4 = knowledgeCollectorUI.elapsedTimeDeactivateKnowledgeCollectorUI_1 - 0.45

        knowledgeCollectorUI.variableScaleStep_1 = MathUtils.smoothstep(knowledgeCollectorUI.elapsedTimeDeactivateKnowledgeCollectorUI_4, 0, 0.2) * 0.7

        knowledgeCollectorUI.ringLeft_1.scale.x = knowledgeCollectorUI.ringLeftInitialScale_1.x - knowledgeCollectorUI.variableScaleStep_1
        knowledgeCollectorUI.ringLeft_1.scale.y = knowledgeCollectorUI.ringLeftInitialScale_1.y - knowledgeCollectorUI.variableScaleStep_1
        knowledgeCollectorUI.ringLeft_2.scale.x = knowledgeCollectorUI.ringLeftInitialScale_2.x - knowledgeCollectorUI.variableScaleStep_1
        knowledgeCollectorUI.ringLeft_2.scale.y = knowledgeCollectorUI.ringLeftInitialScale_2.y - knowledgeCollectorUI.variableScaleStep_1
        knowledgeCollectorUI.ringLeft_3.scale.x = knowledgeCollectorUI.ringLeftInitialScale_3.x - knowledgeCollectorUI.variableScaleStep_1
        knowledgeCollectorUI.ringLeft_3.scale.y = knowledgeCollectorUI.ringLeftInitialScale_3.y - knowledgeCollectorUI.variableScaleStep_1
        knowledgeCollectorUI.ringLeft_4.scale.x = knowledgeCollectorUI.ringLeftInitialScale_4.x - knowledgeCollectorUI.variableScaleStep_1
        knowledgeCollectorUI.ringLeft_4.scale.y = knowledgeCollectorUI.ringLeftInitialScale_4.y - knowledgeCollectorUI.variableScaleStep_1
        knowledgeCollectorUI.ringLeft_5.scale.x = knowledgeCollectorUI.ringLeftInitialScale_5.x - knowledgeCollectorUI.variableScaleStep_1
        knowledgeCollectorUI.ringLeft_5.scale.y = knowledgeCollectorUI.ringLeftInitialScale_5.y - knowledgeCollectorUI.variableScaleStep_1
        knowledgeCollectorUI.ringLeft_6.scale.x = knowledgeCollectorUI.ringLeftInitialScale_6.x - knowledgeCollectorUI.variableScaleStep_1
        knowledgeCollectorUI.ringLeft_6.scale.y = knowledgeCollectorUI.ringLeftInitialScale_6.y - knowledgeCollectorUI.variableScaleStep_1

        knowledgeCollectorUI.ringRight_1.scale.x = knowledgeCollectorUI.ringRightInitialScale_1.x - knowledgeCollectorUI.variableScaleStep_1
        knowledgeCollectorUI.ringRight_1.scale.y = knowledgeCollectorUI.ringRightInitialScale_1.y - knowledgeCollectorUI.variableScaleStep_1
        knowledgeCollectorUI.ringRight_2.scale.x = knowledgeCollectorUI.ringRightInitialScale_2.x - knowledgeCollectorUI.variableScaleStep_1
        knowledgeCollectorUI.ringRight_2.scale.y = knowledgeCollectorUI.ringRightInitialScale_2.y - knowledgeCollectorUI.variableScaleStep_1
        knowledgeCollectorUI.ringRight_3.scale.x = knowledgeCollectorUI.ringRightInitialScale_3.x - knowledgeCollectorUI.variableScaleStep_1
        knowledgeCollectorUI.ringRight_3.scale.y = knowledgeCollectorUI.ringRightInitialScale_3.y - knowledgeCollectorUI.variableScaleStep_1
        knowledgeCollectorUI.ringRight_4.scale.x = knowledgeCollectorUI.ringRightInitialScale_4.x - knowledgeCollectorUI.variableScaleStep_1
        knowledgeCollectorUI.ringRight_4.scale.y = knowledgeCollectorUI.ringRightInitialScale_4.y - knowledgeCollectorUI.variableScaleStep_1
        knowledgeCollectorUI.ringRight_5.scale.x = knowledgeCollectorUI.ringRightInitialScale_5.x - knowledgeCollectorUI.variableScaleStep_1
        knowledgeCollectorUI.ringRight_5.scale.y = knowledgeCollectorUI.ringRightInitialScale_5.y - knowledgeCollectorUI.variableScaleStep_1
        knowledgeCollectorUI.ringRight_6.scale.x = knowledgeCollectorUI.ringRightInitialScale_6.x - knowledgeCollectorUI.variableScaleStep_1
        knowledgeCollectorUI.ringRight_6.scale.y = knowledgeCollectorUI.ringRightInitialScale_6.y - knowledgeCollectorUI.variableScaleStep_1
    }
    else if(knowledgeCollectorUI.elapsedTimeDeactivateKnowledgeCollectorUI_1 > 0.65)
    {
        //set exact value
        for(let i = 1 ; i <= 6 ; i++)
        {
            //set exact value
            knowledgeCollectorUI[`ringLeft_${i}`].material.opacity = 0
            knowledgeCollectorUI[`ringRight_${i}`].material.opacity = 0
        }

        knowledgeCollector.animationProtectionShield = false
        knowledgeCollectorUI.cancelAnimationFrameDeactivateKnowledgeCollectorUI = true
        knowledgeCollectorUI.clockDeactivateKnowledgeCollectorUI.stop()
    }
    
    knowledgeCollectorUI.requestAnimationFrameDeactivateKnowledgeCollectorUI = window.requestAnimationFrame(deactivateKnowledgeCollectorUIAnimationLoop)
    if(knowledgeCollectorUI.cancelAnimationFrameDeactivateKnowledgeCollectorUI) cancelAnimationFrame(knowledgeCollectorUI.requestAnimationFrameDeactivateKnowledgeCollectorUI)
}









export
{
    deactivateKnowledgeCollectorUIAnimationLoop
}