import {knowledgeCollectorUI} from "./knowledgeCollectorUI"
import {MathUtils} from "three"









const opacityAnimationLoop = (previousContentCarrier, currentContentCarrier) =>
{
    knowledgeCollectorUI.elapsedTimeOpacity = knowledgeCollectorUI.clockOpacity.getElapsedTime()

    if(knowledgeCollectorUI.elapsedTimeOpacity < 0.3)
    {
        knowledgeCollectorUI[previousContentCarrier].material.opacity = 0.7 - MathUtils.smoothstep(knowledgeCollectorUI.elapsedTimeOpacity, 0, 0.3) * 0.7
        knowledgeCollectorUI[currentContentCarrier].material.opacity = 0 +  MathUtils.smoothstep(knowledgeCollectorUI.elapsedTimeOpacity, 0 , 0.3) * 0.7
        knowledgeCollectorUI.graph[knowledgeCollectorUI.graph.previousNode].mesh.material.opacity = 0.7 - MathUtils.smoothstep(knowledgeCollectorUI.elapsedTimeOpacity, 0, 0.3) * 0.7
        knowledgeCollectorUI.graph[knowledgeCollectorUI.graph.currentNode].mesh.material.opacity = MathUtils.smoothstep(knowledgeCollectorUI.elapsedTimeOpacity, 0, 0.3) * 0.7
    }
    else
    {
        //set exact opacity
        knowledgeCollectorUI[previousContentCarrier].opacity = 0
        knowledgeCollectorUI[currentContentCarrier].opacity = 0.7
        
        knowledgeCollectorUI.cancelAnimationFrameOpacity = true
        knowledgeCollectorUI.clockOpacity.stop()
    }

    knowledgeCollectorUI.animationFrameOpacity = requestAnimationFrame(() => {opacityAnimationLoop(previousContentCarrier, currentContentCarrier)})
    if(knowledgeCollectorUI.cancelAnimationFrameOpacity) window.cancelAnimationFrame(knowledgeCollectorUI.animationFrameOpacity)
}









export
{
    opacityAnimationLoop
}