import {generateKnowledgeCollectorUIRingSystem} from "../knowledgeCollectorUI/generateKnowledgeCollectorUIRingSystem"
import { Mesh, PlaneGeometry, ShaderMaterial,LoadingManager, MathUtils } from "three"
import { loadingObj } from "./loadingObj"
import { scene } from "../../script"
import loadingVertexShader from "../shader/loading/vertexShader.glsl"
import loadingFragmentShader from "../shader/loading/fragmentShader.glsl"
import {generateKnowledgeCollectorUIContent} from "../knowledgeCollectorUI/generateKnowledgeCollectorUIContent"
import {generateKnowledgeCollectorUIGroupHierarchy} from "../knowledgeCollectorUI/generateKnowledgeCollectorUIGroupHierarchy"
import {generateResourceInterfaceBeam} from "../resourceInterface/generateResourceInterfaceBeam"
import {generateResourceInterfaceParticleSystem} from "../resourceInterface/generateResourceInterfaceParticleSystem"
import {ringGroupStructure} from "../teleporter/teleporterFunctionality"
import {generateTeleporterGroupHierarchy} from "../teleporter/generateTeleporterGroupHierarchy"
import { configureTeleporterRings } from "../teleporter/configureTeleporterRings"
import {teleporterAnimationLoop} from "../teleporter/teleporterAnimationLoop.js"









const generateLoadingOverlay = () =>
{
    loadingObj.overlayMaterial = new ShaderMaterial({
        transparent: true,
        wireframe: false,
        uniforms: { 
            uAlpha: {value: 1.0}
        },
        vertexShader: loadingVertexShader,
        fragmentShader: loadingFragmentShader,
    })
    loadingObj.overlayGeometry = new PlaneGeometry(2, 2, 1, 1)//heigt u width auf 2 clipSpace ausfuelen
    loadingObj.overlayMesh = new Mesh(loadingObj.overlayGeometry, loadingObj.overlayMaterial)
    scene.add(loadingObj.overlayMesh)

    loadingObj.loadingBar = document.querySelector(".loading-bar")
}









const loadingManager = new LoadingManager(
    () =>
    {
        loadingObj.loadingComplete = true
        console.log("hit")
        //nutze setTimeOut um CSS animationszeit zu ueberbruecken
        setTimeout(loadingBarScaleOut, 700)
        setTimeout(loadingOverlayAnimation, 2200)
        
        configureTeleporterRings()
        generateTeleporterGroupHierarchy()
        setTimeout(teleporterAnimationLoop, 3000)//der zweite schleifen aufruf dauert ca 2sec, CPU voll ausgelastet!

        generateResourceInterfaceBeam()
        generateResourceInterfaceParticleSystem()
        generateKnowledgeCollectorUIRingSystem()
        generateKnowledgeCollectorUIContent()
        generateKnowledgeCollectorUIGroupHierarchy()
    }
    ,
    (url, itemsLoaded, itemsTotal) =>
    {
        loadingBarProgress(itemsLoaded, itemsTotal)
    }
    ,
    (url) =>
    {
        console.log(`error on: ${url}`)
    }
)









const loadingBarProgress = (itemsLoaded, itemsTotal) =>
{
    loadingObj.loadingProgress = itemsLoaded / itemsTotal
    loadingObj.loadingBar.style.transform = `scale(${loadingObj.loadingProgress})`
}


const loadingBarScaleOut = () =>
{
    loadingObj.loadingBar.style.transform = ""//transform in css schwaechen
    loadingObj.loadingBar.classList.add("loaded")
}








const loadingOverlayAnimation = () =>
{
    loadingObj.elapsedTIme = loadingObj.time_0.getElapsedTime()

    if(loadingObj.elapsedTIme < 4)
    {
        overlayOpacity()
    }

    if(loadingObj.elapsedTIme > 4)
    {
        cleanLoadingOverlay()
        loadingObj.time_0.stop()
        loadingObj.cancelAnimationFrame = true
    }

    loadingObj.animationFrame = requestAnimationFrame(loadingOverlayAnimation)
    if(loadingObj.cancelAnimationFrame) window.cancelAnimationFrame(loadingObj.animationFrame)
}









const overlayOpacity = () =>
{
    loadingObj.overlayMaterial.uniforms.uAlpha.value = 1 - (MathUtils.smoothstep(loadingObj.elapsedTIme, 0, 3) * 1)

}









const cleanLoadingOverlay = () =>
{
    loadingObj.overlayGeometry.dispose()
    loadingObj.overlayMaterial.dispose()
    scene.remove(loadingObj.overlayMesh)

    loadingObj.loadingBar.remove()
}








export
{
    loadingManager,
    generateLoadingOverlay
}