import {translationAnimationLoop} from "./translationAnimationLoop.js"
import {initializeTranslation} from "./initializeTranslation.js"
import {opacityAnimationLoop} from "./opacityAnimationLoop.js"
import {knowledgeCollectorUI} from "./knowledgeCollectorUI.js"
import {deletePreviousNode} from "./deletePreviousNode.js"
import {initializeOpacity} from "./initializeOpacity.js"
import {MathUtils} from "three"









const scaleAnimationLoop = () =>
{
    knowledgeCollectorUI.elapsedTimeScale = knowledgeCollectorUI.clockScale.getElapsedTime()

    if(knowledgeCollectorUI.elapsedTimeScale < 0.3)
    {
        knowledgeCollectorUI.variablePositionStep_1 = MathUtils.smoothstep(knowledgeCollectorUI.elapsedTimeScale, 0, 0.3) * 0.05 * knowledgeCollectorUI.scaleDirectionFactor
        knowledgeCollectorUI.variablePositionStep_2 = MathUtils.smoothstep(knowledgeCollectorUI.elapsedTimeScale, 0, 0.3) * 0.06 * knowledgeCollectorUI.scaleDirectionFactor
        knowledgeCollectorUI.variablePositionStep_3 = MathUtils.smoothstep(knowledgeCollectorUI.elapsedTimeScale, 0, 0.3) * 0.07 * knowledgeCollectorUI.scaleDirectionFactor
        knowledgeCollectorUI.variablePositionStep_4 = MathUtils.smoothstep(knowledgeCollectorUI.elapsedTimeScale, 0, 0.3) * 0.085 * knowledgeCollectorUI.scaleDirectionFactor
        knowledgeCollectorUI.variablePositionStep_5 = MathUtils.smoothstep(knowledgeCollectorUI.elapsedTimeScale, 0, 0.3) * 0.1 * knowledgeCollectorUI.scaleDirectionFactor
        knowledgeCollectorUI.variablePositionStep_6 = MathUtils.smoothstep(knowledgeCollectorUI.elapsedTimeScale, 0, 0.3) * 0.115 * knowledgeCollectorUI.scaleDirectionFactor

        knowledgeCollectorUI.ringLeft_1.position.y = knowledgeCollectorUI.ringLeftInitialPosition_1.y - knowledgeCollectorUI.variablePositionStep_1
        knowledgeCollectorUI.ringLeft_2.position.y = knowledgeCollectorUI.ringLeftInitialPosition_2.y - knowledgeCollectorUI.variablePositionStep_2
        knowledgeCollectorUI.ringLeft_3.position.y = knowledgeCollectorUI.ringLeftInitialPosition_3.y - knowledgeCollectorUI.variablePositionStep_3
        knowledgeCollectorUI.ringLeft_4.position.y = knowledgeCollectorUI.ringLeftInitialPosition_4.y - knowledgeCollectorUI.variablePositionStep_4
        knowledgeCollectorUI.ringLeft_5.position.y = knowledgeCollectorUI.ringLeftInitialPosition_5.y - knowledgeCollectorUI.variablePositionStep_5
        knowledgeCollectorUI.ringLeft_6.position.y = knowledgeCollectorUI.ringLeftInitialPosition_6.y - knowledgeCollectorUI.variablePositionStep_6
        
        knowledgeCollectorUI.ringRight_1.position.y = knowledgeCollectorUI.ringRightInitialPosition_1.y + knowledgeCollectorUI.variablePositionStep_1
        knowledgeCollectorUI.ringRight_2.position.y = knowledgeCollectorUI.ringRightInitialPosition_2.y + knowledgeCollectorUI.variablePositionStep_2
        knowledgeCollectorUI.ringRight_3.position.y = knowledgeCollectorUI.ringRightInitialPosition_3.y + knowledgeCollectorUI.variablePositionStep_3
        knowledgeCollectorUI.ringRight_4.position.y = knowledgeCollectorUI.ringRightInitialPosition_4.y + knowledgeCollectorUI.variablePositionStep_4
        knowledgeCollectorUI.ringRight_5.position.y = knowledgeCollectorUI.ringRightInitialPosition_5.y + knowledgeCollectorUI.variablePositionStep_5
        knowledgeCollectorUI.ringRight_6.position.y = knowledgeCollectorUI.ringRightInitialPosition_6.y + knowledgeCollectorUI.variablePositionStep_6

        knowledgeCollectorUI.contentCarrier_3.scale.y = knowledgeCollectorUI.contentCarrierInitialScale_3.y + MathUtils.smoothstep(knowledgeCollectorUI.elapsedTimeScale, 0, 0.3) * 0.6 * knowledgeCollectorUI.scaleDirectionFactor
    }

    if(knowledgeCollectorUI.elapsedTimeScale > 0.3)
    {
        //set exact form
        knowledgeCollectorUI.contentCarrier_3.scale.y = knowledgeCollectorUI.gesturesSwipeDirection === "up" ? 0.7 : 0.1

        knowledgeCollectorUI.cancelAnimationFrameScale = true
        knowledgeCollectorUI.clockScale.stop()

        if(knowledgeCollectorUI.previousLevel === "3" && knowledgeCollectorUI.currentLevel === "2")
        {
            initializeTranslation()
            translationAnimationLoop()
            initializeOpacity()
            opacityAnimationLoop("contentCarrier_3", "contentCarrier_2")
        }
        else
        {
            deletePreviousNode()
        }
    }

    knowledgeCollectorUI.animationFrameScale = requestAnimationFrame(scaleAnimationLoop)
    if(knowledgeCollectorUI.cancelAnimationFrameScale) window.cancelAnimationFrame(knowledgeCollectorUI.animationFrameScale)
}









export
{
    scaleAnimationLoop
}