//Groups
const firstPersonGroup = new THREE.Group()
firstPersonGroup.name = "firstPersonGroup"
firstPersonGroup.rotation.y = Math.PI
firstPersonGroup.position.set( 0, 2, 95.335)//95.335
scene.add( firstPersonGroup )











//Camera
const firstPersonCam = new THREE.PerspectiveCamera(50, sizesObj.width / sizesObj.height, 0.2, 120)
firstPersonCam.name = "firstPersonCam" 
firstPersonGroup.add( firstPersonCam )
firstPersonCam.rotation.y = Math.PI
firstPersonCam.position.set( 0, 0, 0.04 )

const studyCam = new THREE.PerspectiveCamera( 45, sizesObj.width / sizesObj.height, 0.1, 1000 )
studyCam.name = "studyCam"
studyCam.position.set( 0, 3.28, -45 )
scene.add( studyCam )

const cameraCollection = {
    firstPersonCam: firstPersonCam,
    studyCam: studyCam,
    activeCam: firstPersonCam
}









//Controls
const orbitControlStudy = new OrbitControls( studyCam, canvas )
orbitControlStudy.enabled = false
orbitControlStudy.enableDamping = true
orbitControlStudy.dampingFactor = 0.11
orbitControlStudy.enablePan = false
orbitControlStudy.enableZoom = true
orbitControlStudy.minDistance = 1
orbitControlStudy.maxDistance = 16.5
orbitControlStudy.maxPolarAngle = 1.65
orbitControlStudy.target = new THREE.Vector3( 0, 2, -60.57148742675781 )









