//Renderer
const renderer = new THREE.WebGLRenderer({
    canvas: canvas,
    antialias: true,
    powerPreference: "high-performance"
})
renderer.outputEncoding = THREE.sRGBEncoding
//renderer.physicallyCorrectLights = true
//renderer.toneMapping = THREE.CineonToneMapping
//renderer.toneMapping = THREE.ACESFilmicToneMapping
//renderer.toneMapping = THREE.LinearToneMapping
//renderer.toneMappingExposure = 1
renderer.setSize( sizesObj.width, sizesObj.height )
renderer.setPixelRatio( Math.min( window.devicePixelRatio, 2 ) )
//console.log(renderer.capabilities.getMaxAnisotropy())//! muss vor assinment verarbeitet werden







//Animation
const animation = () =>
{
    stats.begin()



    if(studyModeObj.active) orbitControlStudy.update()



    if(!gateObj.scanRun && loadingObj.loadingComplete && firstPersonGroup.position.distanceTo(gateObj.gateTop.position) < 22)
    {
        gateObj.scanRun = true
        setBiometricLaserScann()
    }
    if(gateObj.open && firstPersonGroup.position.distanceTo(gateObj.referenzMeshClosing.position) < 36)
    {
        gateObj.open = false
        gateObj.cancelAnimationFrame_1 = false
        gateMechanicClosing()   
    }



    renderer.render( scene, cameraCollection.activeCam )



    stats.end()



    window.requestAnimationFrame(animation)
}
animation()








//EventListeners
window.addEventListener( "resize", () =>
{
    sizesObj.width = window.innerWidth
    sizesObj.height = window.innerHeight
   
    firstPersonCam.aspect = sizesObj.width / sizesObj.height
    firstPersonCam.updateProjectionMatrix()

    studyCam.aspect = sizesObj.width / sizesObj.height
    studyCam.updateProjectionMatrix()

    renderer.setSize( sizesObj.width, sizesObj.height )
    renderer.setPixelRatio( Math.min( window.devicePixelRatio, 2 ) )
})









