import {knowledgeCollectorUI} from "./knowledgeCollectorUI.js"
import {knowledgeCollector} from "../knowledgeCollector/knowledgeCollector.js"







const initializeDeactivateKnowledgeCollectorUI = () =>
{
    knowledgeCollectorUI.ringLeftInitialPosition_1 = knowledgeCollectorUI.ringLeft_1.position.clone()
    knowledgeCollectorUI.ringLeftInitialPosition_2 = knowledgeCollectorUI.ringLeft_2.position.clone()
    knowledgeCollectorUI.ringLeftInitialPosition_3 = knowledgeCollectorUI.ringLeft_3.position.clone()
    knowledgeCollectorUI.ringLeftInitialPosition_4 = knowledgeCollectorUI.ringLeft_4.position.clone()
    knowledgeCollectorUI.ringLeftInitialPosition_5 = knowledgeCollectorUI.ringLeft_5.position.clone()
    knowledgeCollectorUI.ringLeftInitialPosition_6 = knowledgeCollectorUI.ringLeft_6.position.clone()
    knowledgeCollectorUI.ringRightInitialPosition_1 = knowledgeCollectorUI.ringRight_1.position.clone()
    knowledgeCollectorUI.ringRightInitialPosition_2 = knowledgeCollectorUI.ringRight_2.position.clone()
    knowledgeCollectorUI.ringRightInitialPosition_3 = knowledgeCollectorUI.ringRight_3.position.clone()
    knowledgeCollectorUI.ringRightInitialPosition_4 = knowledgeCollectorUI.ringRight_4.position.clone()
    knowledgeCollectorUI.ringRightInitialPosition_5 = knowledgeCollectorUI.ringRight_5.position.clone()
    knowledgeCollectorUI.ringRightInitialPosition_6 = knowledgeCollectorUI.ringRight_6.position.clone()

    knowledgeCollectorUI.ringLeftInitialRotation_1 = knowledgeCollectorUI.ringLeft_1.rotation.clone()
    knowledgeCollectorUI.ringLeftInitialRotation_2 = knowledgeCollectorUI.ringLeft_2.rotation.clone()
    knowledgeCollectorUI.ringLeftInitialRotation_3 = knowledgeCollectorUI.ringLeft_3.rotation.clone()
    knowledgeCollectorUI.ringLeftInitialRotation_4 = knowledgeCollectorUI.ringLeft_4.rotation.clone()
    knowledgeCollectorUI.ringLeftInitialRotation_5 = knowledgeCollectorUI.ringLeft_5.rotation.clone()
    knowledgeCollectorUI.ringLeftInitialRotation_6 = knowledgeCollectorUI.ringLeft_6.rotation.clone()
    knowledgeCollectorUI.ringRightInitialRotation_1 = knowledgeCollectorUI.ringRight_1.rotation.clone()
    knowledgeCollectorUI.ringRightInitialRotation_2 = knowledgeCollectorUI.ringRight_2.rotation.clone()
    knowledgeCollectorUI.ringRightInitialRotation_3 = knowledgeCollectorUI.ringRight_3.rotation.clone()
    knowledgeCollectorUI.ringRightInitialRotation_4 = knowledgeCollectorUI.ringRight_4.rotation.clone()
    knowledgeCollectorUI.ringRightInitialRotation_5 = knowledgeCollectorUI.ringRight_5.rotation.clone()
    knowledgeCollectorUI.ringRightInitialRotation_6 = knowledgeCollectorUI.ringRight_6.rotation.clone()

    knowledgeCollectorUI.ringLeftInitialScale_1 = knowledgeCollectorUI.ringLeft_1.scale.clone()
    knowledgeCollectorUI.ringLeftInitialScale_2 = knowledgeCollectorUI.ringLeft_2.scale.clone()
    knowledgeCollectorUI.ringLeftInitialScale_3 = knowledgeCollectorUI.ringLeft_3.scale.clone()
    knowledgeCollectorUI.ringLeftInitialScale_4 = knowledgeCollectorUI.ringLeft_4.scale.clone()
    knowledgeCollectorUI.ringLeftInitialScale_5 = knowledgeCollectorUI.ringLeft_5.scale.clone()
    knowledgeCollectorUI.ringLeftInitialScale_6 = knowledgeCollectorUI.ringLeft_6.scale.clone()
    knowledgeCollectorUI.ringRightInitialScale_1 = knowledgeCollectorUI.ringRight_1.scale.clone()
    knowledgeCollectorUI.ringRightInitialScale_2 = knowledgeCollectorUI.ringRight_2.scale.clone()
    knowledgeCollectorUI.ringRightInitialScale_3 = knowledgeCollectorUI.ringRight_3.scale.clone()
    knowledgeCollectorUI.ringRightInitialScale_4 = knowledgeCollectorUI.ringRight_4.scale.clone()
    knowledgeCollectorUI.ringRightInitialScale_5 = knowledgeCollectorUI.ringRight_5.scale.clone()
    knowledgeCollectorUI.ringRightInitialScale_6 = knowledgeCollectorUI.ringRight_6.scale.clone()
    
    knowledgeCollectorUI.initialOpacity_1 = 0.8
    knowledgeCollectorUI.initialOpacity_2 = 0.6

    knowledgeCollectorUI.enabled = false
    knowledgeCollectorUI.oneTimeIteration = false
    knowledgeCollector.animationProtectionShield = true
    knowledgeCollectorUI.cancelAnimationFrameDeactivateKnowledgeCollectorUI = false
    knowledgeCollectorUI.clockDeactivateKnowledgeCollectorUI.start()
}









export
{
    initializeDeactivateKnowledgeCollectorUI
}