import {knowledgeCollector} from "./knowledgeCollector.js"









const setGroupHierarchyLocationForLituusSpiral = () =>
{
    knowledgeCollector.group_1.rotation.set(0, 0, 0)
    knowledgeCollector.group_2.rotation.set(-Math.PI * 0.42, 0, 0)
    knowledgeCollector.group_3.rotation.set(0, -Math.PI * 0.1, 0)
    knowledgeCollector.group_4.rotation.set(0, 0, 0)
    knowledgeCollector.group_5.rotation.set(0, 0, 0)
    knowledgeCollector.group_6.rotation.set(0, Math.PI, 0)
    knowledgeCollector.group_6.position.set(0, 0, 4.8)//je nach device ratio anpassen damit collector explotion voll auf screen ist
    knowledgeCollector.group_5.position.set(0, -0.6, 5.8)
}









export
{
    setGroupHierarchyLocationForLituusSpiral
}