import {Mesh, MeshBasicMaterial, PlaneGeometry} from "three"
import {resourceInterface} from "./resourceInterface"
import {textures} from "../textures/texturesj"
import {scene} from "../../script"









const generateResourceInterfaceBeam = () =>
{
    const beamGeometry = new PlaneGeometry(2, 4)
    
    const beamMaterial = new MeshBasicMaterial()
    beamMaterial.map = textures.resourceInterfaceBeam
    beamMaterial.transparent = true
    beamMaterial.depthTest = true
    beamMaterial.depthWrite = false
    beamMaterial.opacity = 1
    
    resourceInterface.beam = new Mesh(beamGeometry, beamMaterial)
    resourceInterface.beam.position.set(-3.42, 1.2, 43.35)
    resourceInterface.beam.scale.x = 0
    resourceInterface.beam.visible = false
   
    scene.add(resourceInterface.beam)
}









export
{
    generateResourceInterfaceBeam
}









