import {knowledgeCollectorUI} from "./knowledgeCollectorUI"








//setgesturesSwipeDirectionValues
const gesturesSwipeDirection = () =>
{
    if(Math.abs(knowledgeCollectorUI.delta.x) > Math.abs(knowledgeCollectorUI.delta.y))
    {
        if(knowledgeCollectorUI.delta.x > 0)
        {
            knowledgeCollectorUI.gesturesSwipeDirection = "right"
            knowledgeCollectorUI.rotationDirectionFactor = 1
        } 
        if(knowledgeCollectorUI.delta.x < 0)
        {
            knowledgeCollectorUI.gesturesSwipeDirection = "left"
            knowledgeCollectorUI.rotationDirectionFactor = -1
        }
    }
    
    if(Math.abs(knowledgeCollectorUI.delta.x) < Math.abs(knowledgeCollectorUI.delta.y))
    {
        if(knowledgeCollectorUI.delta.y < 0)
        {
            knowledgeCollectorUI.gesturesSwipeDirection = "up"
            knowledgeCollectorUI.elevationDirectionFactor = 1
            knowledgeCollectorUI.scaleDirectionFactor = 1
        }
        if(knowledgeCollectorUI.delta.y > 0)
        {
            knowledgeCollectorUI.gesturesSwipeDirection = "down"
            knowledgeCollectorUI.elevationDirectionFactor = -1
            knowledgeCollectorUI.scaleDirectionFactor = -1
        }
    }
}









export
{
    gesturesSwipeDirection
}