import {knowledgeCollectorUI} from "./knowledgeCollectorUI"
import {deletePreviousNode} from "./deletePreviousNode.js"
import {scaleAnimationLoop} from "./scaleAnimationLoop.js"
import {initializeScale} from "./initializeScale"
import {MathUtils} from "three"









const translationAnimationLoop = () =>
{
    knowledgeCollectorUI.elapsedTimeTranslation = knowledgeCollectorUI.clockTranslation.getElapsedTime()
    
    if(knowledgeCollectorUI.elapsedTimeTranslation < 0.3)
    {
        knowledgeCollectorUI.variableRotationStep_1 = MathUtils.smoothstep(knowledgeCollectorUI.elapsedTimeTranslation, 0, 0.4) * Math.PI * 0.4
        knowledgeCollectorUI.variableRotationStep_2 = MathUtils.smoothstep(knowledgeCollectorUI.elapsedTimeTranslation, 0, 0.4) * Math.PI * 0.48
        knowledgeCollectorUI.variableRotationStep_3 = MathUtils.smoothstep(knowledgeCollectorUI.elapsedTimeTranslation, 0, 0.4) * Math.PI * 0.56
        knowledgeCollectorUI.variableRotationStep_4 = MathUtils.smoothstep(knowledgeCollectorUI.elapsedTimeTranslation, 0, 0.4) * Math.PI * 0.64
        knowledgeCollectorUI.variableRotationStep_5 = MathUtils.smoothstep(knowledgeCollectorUI.elapsedTimeTranslation, 0, 0.4) * Math.PI * 0.72
        knowledgeCollectorUI.variableRotationStep_6 = MathUtils.smoothstep(knowledgeCollectorUI.elapsedTimeTranslation, 0, 0.4) * Math.PI * 0.80

        knowledgeCollectorUI.ringLeft_1.rotation.z = knowledgeCollectorUI.ringLeftInitialRotation_1.z - knowledgeCollectorUI.variableRotationStep_1 * knowledgeCollectorUI.elevationDirectionFactor
        knowledgeCollectorUI.ringLeft_2.rotation.z = knowledgeCollectorUI.ringLeftInitialRotation_2.z - knowledgeCollectorUI.variableRotationStep_2 * knowledgeCollectorUI.elevationDirectionFactor
        knowledgeCollectorUI.ringLeft_3.rotation.z = knowledgeCollectorUI.ringLeftInitialRotation_3.z - knowledgeCollectorUI.variableRotationStep_3 * knowledgeCollectorUI.elevationDirectionFactor
        knowledgeCollectorUI.ringLeft_4.rotation.z = knowledgeCollectorUI.ringLeftInitialRotation_4.z - knowledgeCollectorUI.variableRotationStep_4 * knowledgeCollectorUI.elevationDirectionFactor
        knowledgeCollectorUI.ringLeft_5.rotation.z = knowledgeCollectorUI.ringLeftInitialRotation_5.z - knowledgeCollectorUI.variableRotationStep_5 * knowledgeCollectorUI.elevationDirectionFactor
        knowledgeCollectorUI.ringLeft_6.rotation.z = knowledgeCollectorUI.ringLeftInitialRotation_6.z - knowledgeCollectorUI.variableRotationStep_6 * knowledgeCollectorUI.elevationDirectionFactor
        
        knowledgeCollectorUI.ringRight_1.rotation.z = knowledgeCollectorUI.ringRightInitialRotation_1.z + knowledgeCollectorUI.variableRotationStep_1 * knowledgeCollectorUI.elevationDirectionFactor
        knowledgeCollectorUI.ringRight_2.rotation.z = knowledgeCollectorUI.ringRightInitialRotation_2.z + knowledgeCollectorUI.variableRotationStep_2 * knowledgeCollectorUI.elevationDirectionFactor
        knowledgeCollectorUI.ringRight_3.rotation.z = knowledgeCollectorUI.ringRightInitialRotation_3.z + knowledgeCollectorUI.variableRotationStep_3 * knowledgeCollectorUI.elevationDirectionFactor
        knowledgeCollectorUI.ringRight_4.rotation.z = knowledgeCollectorUI.ringRightInitialRotation_4.z + knowledgeCollectorUI.variableRotationStep_4 * knowledgeCollectorUI.elevationDirectionFactor
        knowledgeCollectorUI.ringRight_5.rotation.z = knowledgeCollectorUI.ringRightInitialRotation_5.z + knowledgeCollectorUI.variableRotationStep_5 * knowledgeCollectorUI.elevationDirectionFactor
        knowledgeCollectorUI.ringRight_6.rotation.z = knowledgeCollectorUI.ringRightInitialRotation_6.z + knowledgeCollectorUI.variableRotationStep_6 * knowledgeCollectorUI.elevationDirectionFactor

        knowledgeCollectorUI.group_0.position.z = knowledgeCollectorUI.groupInitialPosition_0.z - (MathUtils.smoothstep(knowledgeCollectorUI.elapsedTimeTranslation, 0, 0.3) * 0.7 * knowledgeCollectorUI.elevationDirectionFactor)
    }

    if(knowledgeCollectorUI.elapsedTimeTranslation > 0.3)
    {
        //set exact position
        knowledgeCollectorUI.gesturesSwipeDirection === "up" ? knowledgeCollectorUI.groupTranslationCount_0 ++ : knowledgeCollectorUI.groupTranslationCount_0 --
        knowledgeCollectorUI.group_0.position.z = knowledgeCollectorUI.groupTranslationCount_0 * -0.7

        knowledgeCollectorUI.cancelAnimationFrameTranslation = true
        knowledgeCollectorUI.clockTranslation.stop()

        if(knowledgeCollectorUI.previousLevel === "2" && knowledgeCollectorUI.currentLevel === "3")
        {
            initializeScale()
            scaleAnimationLoop()
        }
        else
        {
            deletePreviousNode()
        }
    }

    knowledgeCollectorUI.animationFrameTranslation = requestAnimationFrame(translationAnimationLoop)
    if(knowledgeCollectorUI.cancelAnimationFrameTranslation) window.cancelAnimationFrame(knowledgeCollectorUI.animationFrameTranslation)
}









export
{
    translationAnimationLoop
}