import {textures} from "../textures/texturesj.js"
import {teleporter} from "./teleporter.js"
import {MeshBasicMaterial} from "three"









const configureTeleporterRings = () =>
{
    teleporter.ringMaterial = new MeshBasicMaterial()
    teleporter.ringMaterial.map = textures.teleporterRings
    
    teleporter.ring_1.material = teleporter.ringMaterial
    teleporter.ring_2.material = teleporter.ringMaterial
    teleporter.ring_3.material = teleporter.ringMaterial
    teleporter.ring_4.material = teleporter.ringMaterial

    teleporter.ring_1.position.set(0, 0, 0)
    teleporter.ring_2.position.set(0, 0, 0)
    teleporter.ring_3.position.set(0, 0, 0)
    teleporter.ring_4.position.set(0, 0, 0)
}









export
{
    configureTeleporterRings
}