import {knowledgeCollectorUI} from "./knowledgeCollectorUI"









const initializeRotation = (group, groupInitialRotation) =>
{
    knowledgeCollectorUI.ringLeftInitialRotation_1 = knowledgeCollectorUI.ringLeft_1.rotation.clone()
    knowledgeCollectorUI.ringLeftInitialRotation_2 = knowledgeCollectorUI.ringLeft_2.rotation.clone()
    knowledgeCollectorUI.ringLeftInitialRotation_3 = knowledgeCollectorUI.ringLeft_3.rotation.clone()
    knowledgeCollectorUI.ringLeftInitialRotation_4 = knowledgeCollectorUI.ringLeft_4.rotation.clone()
    knowledgeCollectorUI.ringLeftInitialRotation_5 = knowledgeCollectorUI.ringLeft_5.rotation.clone()
    knowledgeCollectorUI.ringLeftInitialRotation_6 = knowledgeCollectorUI.ringLeft_6.rotation.clone()
    knowledgeCollectorUI.ringRightInitialRotation_1 = knowledgeCollectorUI.ringRight_1.rotation.clone()
    knowledgeCollectorUI.ringRightInitialRotation_2 = knowledgeCollectorUI.ringRight_2.rotation.clone()
    knowledgeCollectorUI.ringRightInitialRotation_3 = knowledgeCollectorUI.ringRight_3.rotation.clone()
    knowledgeCollectorUI.ringRightInitialRotation_4 = knowledgeCollectorUI.ringRight_4.rotation.clone()
    knowledgeCollectorUI.ringRightInitialRotation_5 = knowledgeCollectorUI.ringRight_5.rotation.clone()
    knowledgeCollectorUI.ringRightInitialRotation_6 = knowledgeCollectorUI.ringRight_6.rotation.clone()
    knowledgeCollectorUI[groupInitialRotation] = knowledgeCollectorUI[group].rotation.clone()
    
    knowledgeCollectorUI.cancelAnimationFrameRotation = false
    knowledgeCollectorUI.clockRotation.start()
}









export
{
    initializeRotation
}